/**
 * Contact Page SCSS
 * Features Page SCSS
 * About Page SCSS
 */


/* partner with us page Design */
.partner-with-page {
  .partner-with-wrap-main {
    position: relative;
    display: inline-block;
    width: 100%;
    padding: 200px 0;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    @media (max-width: 992px) {
      padding: 150px 0 100px;
    }
    @media (max-width: 767px) {
      padding: 70px 0;
    }
    &::after {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background-color: rgba($black, .55);
      content: '';
    }
    .img-wrap {
      height: 0;
      width: 0;
      opacity: 0;
      img {
        height: 0;
        width: 0;
        opacity: 0;
      } 
    }
    .partner-with-detail-wrap {
      position: relative;
      z-index: 1;
      display: inline-block;
      width: 100%;
      .partner-with-detail {
        @include css3-sufix(display, inline-flex);
        @include css3-prefix(flex-wrap, wrap);
        width: 100%;
        .left-content {
          display: inline-block;
          width: 48%;
          margin-right: 4%;
          color: $white;
          @media (max-width: 992px) {
            width: 100%;
            margin-right: 0;
            margin-bottom: 30px;
          }
          h1 {
            color: $white;
          }
          p {
            font-size: 16px;
            line-height: 28px;
            font-weight: 500;
          }
          .social-icons {
            display: inline-block;
            width: 100%;
            margin-top: 10px;
            li {
              display: inline-block;
              margin-right: 20px;
              a {
                display: inline-block;
                color: $white;
                font-size: 22px;
              }
            }
          }
        }
        .right-content {
          display: inline-block;
          width: 48%;
          @media (max-width: 992px) {
            width: 100%;
          }
          .form-item-row {
            display: inline-block;
            width: 100%;
            label {
              display: inline-block;
              width: 100%;
              color: $white;
              font-size: 16px;
              font-weight: 500;
            }
            .form-item {
              display: inline-block;
              width: 100%;
              margin-bottom: 30px;
              vertical-align: top;
              input {
                height: 42px;
                padding: 3px 10px;
                border: 1px solid #CCC;
                border-radius: 2px;
                background-color: #FAFAFA;
                color: $black;
                &:focus {
                  background-color: $white;
                }
              }
              textarea {
                padding: 3px 10px;
                border: 1px solid #CCC;
                border-radius: 2px;
                background-color: #FAFAFA;
                color: $black;
                &:focus {
                  background-color: $white;
                }
              }
            }
            .error {
              color: red;
            }
          }
          .name-field {
            .form-item {
              display: inline-block;
              @include css3-sufix(width, calc(50% - 12px));
              margin-right: 20px;
              &:last-child {
                margin-right: 0;
              }
              @media (max-width: 480px) {
                width: 100%;
                margin-right: 0;
              }
              input {
                margin-bottom: 5px;
              }
              span {
                display: inline-block;
                width: 100%;
                margin-bottom: 8px;
                color: $white;
                font-size: 16px;
              }
            }
          }
          .form-action {
            .btn {
              border-color: $white;
              color: $white;
              font-weight: 500;
              &:hover {
                background-color: $white !important;
                color: $secoundry-color !important;
              }
            }
          }
        }
      }
    }
  }
}

.features-page-main {
  background-color: #272725;
  .features-wrap {
    display: inline-block;
    width: 100%;
    .grids {
      @include css3-sufix(display, flex);
      @include css3-prefix(flex-wrap, wrap);
      width: 100%;
      margin-top: 240px;
      @media (max-width: 1400px) {
        margin-top: 100px;
      }
      @media (max-width: 767px) {
        margin-top: 50px;
      }
      @media (max-width: 640px) {
        margin-top: 25px;
      }
      .grid {
        display: inline-block;
        @include css3-sufix(width, calc(50% - 15px));
        margin-right: 30px;
        margin-bottom: 240px;
        color: $white;
        text-align: center;
        &:nth-child(2n) {
          margin-right: 0;
        }
        @media (max-width: 1400px) {
          margin-bottom: 100px;
        }
        @media (max-width: 992px) {
          width: 100%;
          margin-right: 0;
        }
        @media (max-width: 767px) {
          margin-bottom: 50px;
        }
        @media (max-width: 640px) {
          margin-bottom: 25px;
        }
        img {
          margin-bottom: 30px;
          @media (max-width: 640px) {
            margin-bottom: 20px;
          }
        }
        h2 {
          margin-bottom: 30px;
          font-size: 35px;
          font-weight: 700;
          @media (max-width: 992px) {
            font-size: 28px;
          }
          @media (max-width: 640px) {
            margin-bottom: 20px;
          }
        }
        .detail-text {
          display: inline-block;
          width: 100%;
          h3 {
            font-size: 16px;
            font-weight: 700;
          }
          p {
            font-size: 16px;
            font-weight: 400;
          }
        }
      }
    }
  }
}


.about-page-main {
  background-color: #272725;
  min-height: 80vh;
  .about-wrap {
    display: inline-block;
    width: 100%;
    padding-top: 100px;
    .grids {
      display: inline-block;
      width: 100%;
      .grid {
        @include css3-sufix(display, flex);
        @include css3-prefix(flex-wrap, wrap);
        width: 100%;
        margin-bottom: 50px;
        .left-content {
          width: 50%;
          @media (max-width: 992px) {
            width: 100%;
            margin-bottom: 15px;
          }
        }
        .right-content {
          width: 50%;
          padding-left: 30px;
          color: $white;
          @media (max-width: 992px) {
            width: 100%;
            padding-left: 0;
          }
          h2 {
            margin-bottom: 10px;
            font-size: 35px;
            font-weight: 700;
            @media (max-width: 992px) {
              font-size: 28px;
            }
            @media (max-width: 640px) {
              margin-bottom: 20px;
            }
          }
          .detail-text {
            display: inline-block;
            width: 100%;
            h3 {
              font-size: 16px;
              font-weight: 700;
            }
            p {
              font-size: 16px;
              line-height: 30px;
              font-weight: 400;
            }
          }
        }
        @media (min-width: 992px) {
          &:nth-child(2n) {
            @include css3-prefix(flex-direction, row-reverse);
            .right-content {
              padding-right: 30px;
              padding-left: 0;
            }
          }
        }
      }
    }
  }
}
