/**
 * Header SCSS
 */

.header-newtheme {
  right: 0;
  left: auto;
  background-color: $white;
  padding: 0 20px;
  @include css3-sufix(width, calc(100% - 300px));
  @media (max-width: 767px) {
    @include css3-sufix(width, calc(100% - 46px));
    padding: 0 10px;
  }
  .top-content {
    display: inline-block;
    width: 100%;
    padding: 10px 0;
    text-align: right;
    @media (max-width: 767px) {
      margin-top: 0;
      margin-right: 0;
    }
    .greting-msg {
      float: left;
      display: inline-block;
      margin-top: 5px;
      color: #FFA500;
      @media (max-width: 767px) {
        display: none;
      }
    }
    .main-menu {
      display: inline-block;
      li {
        display: inline-block;
        padding: 0 10px;
        @media (max-width: 767px) {
          padding: 0 5px;
          display: none;
          &:last-child {
            display: inline-block;
          }
        }
        a {
          color: #333;
          &.btn {
            padding: 8px 20px;
            border: none;
            border-radius: 30px;
            background-color: #FF6062;
            color: $white;
            font-size: 12px;
          }
        }
      }
    }
    .menu {
      display: inline-block;
      vertical-align: middle;
      li {
        display: inline-block;
        padding: 0 10px;
        @media (max-width: 767px) {
          padding: 0 5px;
        }
        a {
          color: #333;
          img {
            width: 26px;
          }
        }
      }
    }
    .profile-wrap {
      position: relative;
      display: inline-block;
      margin: 0 10px;
      vertical-align: middle;
      @media (max-width: 767px) {
        margin: 0 5px;
      }
      > a {
        color: $white;
        img {
          height: 34px;
          width: 34px;
          /*border: 2px solid #333;*/
          border-radius: 5px;
          overflow: hidden;
        }
      }
      .profile-option {
        position: absolute;
        @include css3-prefix(top, calc(100% + 10px));
        left: auto;
        right: 0;
        z-index: 99;
        display: none;
        width: 180px;
        @include css3-prefix(box-shadow, 0 0 8px 0 rgba($black, .1));
        ul {
          display: inline-block;
          width: 100%;
          text-align: left;
          li {
            display: inline-block;
            width: 100%;
            border-bottom: 1px solid #DDD;
            a {
              display: inline-block;
              width: 100%;
              padding: 10px 15px;
              background-color: $white;
              color: #1E1E2D;
            }
          }
        }
      }
    }
    .notification-wrap {
      position: relative;
      display: inline-block;
      margin: 0 10px;
      vertical-align: middle;
      @media (max-width: 767px) {
        margin: 0 5px;
      }
      .notification-icon {
        position: relative;
        img {
          width: 26px;
        }
        .noti-bell {
          position: absolute;
          top: 0;
          right: 0;
          display: inline-block;
          width: 20px;
          height: 20px;
          margin: -4px -8px 0 0;
          border-radius: 100%;
          background-color: #FAC9CE;
          color: #F64E60;
          font-size: 10px;
          line-height: 20px;
          text-align: center;
        }
      }
      ul {
        position: absolute;
        @include css3-prefix(top, calc(100% + 10px));
        left: auto;
        right: 0;
        z-index: 99;
        display: none;
        width: 300px;
        background-color: $white;
        @include css3-prefix(box-shadow, 0 0 8px 0 rgba($black, .1));
        text-align: left;
        @media (max-width: 460px) {
          width: 280px;
        }
        li {
          display: inline-block;
          width: 100%;
          padding: 0;
          border-bottom: 1px solid #DDD;
          a {
            display: inline-block;
            width: 100%;
            padding: 10px 15px;
            background-color: #1E1E2D;
            color: $white;
            font-size: 14px;
          }
        }
      }
    }
  }
  .bottom-content {
    display: inline-block;
    width: 100%;
    padding: 10px 0;
    border-top: 1px solid #888;
    .company-name {
      display: inline-block;
      @include css3-sufix(width, calc(100% - 234px));
      vertical-align: middle;
      @media (max-width: 767px) {
        width: 100%;
      }
      h2 {
        margin-bottom: 0;
        font-size: 20px;
        @media (max-width: 1200px) {
          font-size: 16px;
        }
      }
      #clockbox {
        margin-left: 10px;
        padding: 0 10px;
        border-left: 1px solid #888;
        color: #575757;
        font-weight: 700;
        @media (max-width: 1200px) {
          display: inline-block;
          width: 100%;
          margin-left: 0;
          padding: 0;
          border-left: none;
        }
      }
    }
    .alarm-btn {
      display: inline-block;
      max-width: 230px;
      vertical-align: middle;
      @media (max-width: 767px) {
        position: fixed;
        top: 126px;
        right: 0;
        left: auto;
        z-index: 98;
        width: 180px;
      }
      #emergencyAlarm {
        display: inline-block;
        width: 100%;
      }
    }
  }
}
.login-header {
  .header-newtheme {
    background-color: $white;
  }
}
.new-sidemenu-main {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  display: inline-block;
  height: 100vh;
  width: 300px;
  overflow: auto;
  background-color: #1E1E2D;
  @include css3-prefix(transition, .3s all);
  @media (max-width: 767px) {
    top: 0;
    padding: 0 0 20px;
  }
  &.open {
    right: 0;
  }
  .brand-name {
    padding: 20px 0;
    background-color: #1A1A27;
    text-align: center;
    @include css3-prefix(transition, .3s all);
    img {
      max-width: 120px;
    }
  }
  .sidebar-control {
    position: absolute;
    top: 6px;
    right: 6px;
    left: auto;
    padding: 5px 10px;
    border-radius: 5px;
    background-color: rgba($white, .2);
    color: $white;
    font-size: 18px;
    cursor: pointer;
    @include css3-prefix(transition, .3s all);
    &:hover {
      color: #3699FF;
    }
  }
  ul {
    display: inline-block;
    width: 100%;
    padding: 20px 0;
    li {
      display: inline-block;
      width: 100%;
      a {
        display: inline-block;
        width: 100%;
        padding: 10px 12px;
        color: #A2A3B7;
        @include css3-prefix(transition, .2s all);
        i {
          width: 18px;
          margin-right: 20px;
          color: #494B74;
          text-align: center;
        }
      }
      &.active,
      &:hover {
        > a {
	        background-color: #1A1A27;
	        color: $white;
          i {
            color: #3699FF;
          }
      	}
      }
      &.submenu-main {
        > a {
          position: relative;
          &::after {
            position: absolute;
            top: 17px;
            right: 10px;
            left: auto;
            height: 10px;
            width: 10px;
            background-image: url('../images/right-solid.svg');
            background-repeat: no-repeat;
            background-position: center center;
            content: '';
          }
        }
        ul {
          display: none;
          padding-right: 0;
          padding-left: 20px;
        }
        &.active {
        	ul {
        		display: block;
        	}
        }
      }
    }
  }
  .profile-sidebar {
    display: inline-block;
    width: 100%;
    text-align: center;
    @include css3-prefix(transition, .3s all);
    .profile-userpic {
      display: inline-block;
      width: 100%;
      margin-bottom: 10px;
      img {
        height: 100px;
        width: 100px;
        border-radius: 100%;
      }
    }
    .profile-usertitle {
      display: inline-block;
      width: 100%;
      margin-bottom: 10px;
      color: $white;
    }
    .profile-userbuttons {
      .btn {
        background-color: $white;
        color: #01555B;
        border: 2px solid $white;
        transition: .2s all;
        &:hovre,
        &:focus {
          background-color: transparent !important;
          color: $white;
        }
      }
    }
  }
  @media (max-width: 767px) {
    width: 46px;
    overflow-x: hidden;
    .sidebar-control {
      @include css3-prefix(transform, rotate(180deg))
    }
    .brand-name {
      width: 0;
      overflow: hidden;
    }
    ul li a {
      white-space: nowrap;
      &::after {
        display: none;
      }
    }
    .profile-sidebar {
      width: 0;
      overflow: hidden;
    }
    &.shrink {
      width: 300px;
      .sidebar-control {
        @include css3-prefix(transform, rotate(0))
      }
      .brand-name {
        width: 100%;
      }
      ul li a {
        white-space: normal;
        &::after {
          display: inline-block;
        }
      }
      .profile-sidebar {
        width: 100%;
      }
    }
  }
  @media (min-width: 768px) {
    &.shrink {
      width: 46px;
      overflow-x: hidden;
      .sidebar-control {
        @include css3-prefix(transform, rotate(180deg))
      }
      .brand-name {
        width: 0;
        overflow: hidden;
      }
      ul li a {
        white-space: nowrap;
        &::after {
          display: none;
        }
      }
      .profile-sidebar {
        width: 0;
        overflow: hidden;
      }
    }
  }
}

.sidebar-shrink {
  .header-newtheme {
    @include css3-sufix(width, calc(100% - 46px));
  }
}
