/**
 * Chat SCSS
 */

.chat-page-main {
  width: 100%;
  .chat-headng-wrap {
    display: flex;
    width: 100%;
    background-color: #1F1D2D;
    padding: 5px 10px;
    margin-bottom: 20px;
    align-items: center;
    border-radius: 6px;
    @media (max-width: 767px) {
      flex-wrap: wrap;
    }
    h1 {
      display: inline-block;
      width: 310px;
      margin-bottom: 0;
      color: $white;
      font-size: 34px;
      @media (max-width: 1024px) {
        font-size: 26px;
      }
    }
    .concierge-list {
      display: inline-block;
      flex: 1 1 auto;
      text-align: right;
      @media (max-width: 767px) {
        width: 100%;
      }
      li {
        display: inline-block;
        margin: 0 10px;
        .con-name {
          display: inline-block;
          font-size: 16px;
          font-weight: 500;
          color: $white;
          cursor: pointer;
          &:hover {
            color: #FEA857;
          }
        }
        i {
          font-size: 10px;
        }
        #online {
          color: green;
        }
        #offline {
          color: #9A9A9A;
        }
      }
    }
  }
  .chat-wrap {
    @include css3-sufix(display, flex);
    flex-direction: row;
    width: 100%;
    @media (max-width: 1024px) {
      flex-wrap: wrap;
    }
  }
  .chat-listing-wrap {
    @include css3-sufix(display, inline-flex);
    @include css3-prefix(flex-direction, column);
    width: 350px;
    margin-right: 20px;
    padding: 15px 0;
    border-radius: 6px;
    background-color: $white;
    height: calc(100vh - 260px);
    overflow: auto;
    @media (max-width: 1200px) {
      width: 300px;
    }
    @media (max-width: 1024px) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 30px;
    }
    form {
      position: relative;
      display: inline-block;
      width: 100%;
      margin-bottom: 20px;
      padding: 0 15px;
      input {
        width: 100%;
        height: 40px;
        padding: 3px 40px 3px 10px;
        border: none;
        border-radius: 4px;
        background-color: #F3F6F9;
        color: #555;
        font-size: 14px;
        &:focus {
          box-shadow: none;
          outline: none;
        }
      }
      i {
        position: absolute;
        top: 13px;
        right: 25px;
        color: #888;
        font-size: 16px;
      }
    }
    .chatgroup {
      display: inline-block;
      margin: 10px;
      padding-bottom: 3px;
      border-bottom: 1px solid #FFA500;
      color: #FFA500;
      font-size: 20px;
      font-weight: 600;
    }
    .chat-listing {
      width: 100%;
      .chatlist-wrap {
        display: inline-block;
        width: 100%;
        max-height: 240px;
        overflow: auto;
      }
      .chat-list {
        @include css3-sufix(display, inline-flex);
        @include css3-prefix(flex-direction, row);
        align-items: center;
        width: 100%;
        padding: 10px 15px;
        img {
          height: 44px;
          width: 44px;
          margin-right: 10px;
          border-radius: 100%;
        }
        .chat-contact-wrap {
          flex: 1;
          h3 {
            margin-bottom: 0;
            overflow: hidden;
            color: #333;
            font-size: 14px;
            font-weight: 600;
            white-space: nowrap;
            text-overflow: ellipsis;
            cursor: pointer;
          }
          span {
            display: inline-block;
            width: 100%;
            overflow: hidden;
            color: #BCB9C9;
            font-size: 12px;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
        }
        .time-Wrap {
          width: 100px;
          text-align: right;
          .time {
            float: left;
            display: inline-block;
            width: 100%;
            color: #BCB9C9;
            font-size: 12px;
            font-weight: 600;
          }
          .numbers {
            display: inline-block;
            height: 20px;
            width: 20px;
            border-radius: 100%;
            background-color: #1A1A27;
            color: $white;
            font-size: 10px;
            line-height: 20px;
            text-align: center;
          }
        }
        &:hover,
        &.active {
          background-color: #1F1D2D;
          .chat-contact-wrap {
            h3 {
              color: $white;
            }
          }
          .numbers {
            background-color: $white;
            color: #1A1A27;
          }
        }
      }
    } 
  }
  .chat-msg-wrap {
    @include css3-sufix(display, inline-flex);
    @include css3-prefix(flex-direction, column);
    @include css3-sufix(height, calc(100vh - 260px));
    flex: 1;
    border-radius: 6px;
    background-color: $white;
    .chat-heading {
      height: 80px;
      width: 100%;
      padding: 15px 0;
      text-align: center;
      border-bottom: 1px solid #EFEFEF;
      h2 {
        margin-bottom: 0;
        color: #333;
        font-size: 20px;
      }
      span {
        color: #BCB9C9;
        font-size: 14px;
        i {
          font-size: 8px;
        }
        &.active {
          i {
            color: green;
          }
        }
      }
    }
    .chat-msg-wrap-main {
      display: inline-block;
      width: 100%;
      padding: 20px 30px;
      flex: 1;
      overflow: auto;
      @media (max-width: 767px) {
        padding: 20px 10px;
      }
      .chat_msg_wrap {
        margin-bottom: 10px;
      }
      .contact-info {
        @include css3-sufix(display, flex);
        @include css3-prefix(flex-direction, row);
        align-items: center;
        margin-bottom: 5px;
        img {
          height: 44px;
          width: 44px;
          border-radius: 100%;
        }
        h3 {
          margin: 0 10px;
          overflow: hidden;
          color: #333;
          font-size: 14px;
          font-weight: 600;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        span {
          display: inline-block;
          color: #BCB9C9;
          font-size: 12px;
        }
      }
      .msg-wrap {
        display: inline-block;
        width: 100%;
        .msg {
          display: inline-block;
          padding: 10px 15px;
          border-radius: 4px; 
          background-color: #CAF7F5;
          color: #8088A0;
          font-size: 14px;
          font-weight: 600;
        }
      }
      .msg-sent-wrap {
        .contact-info {
          flex-direction: row-reverse;
        }
        .msg-wrap {
          text-align: right;
          .msg {
            background-color: #E1F0FF;
          }
        }
      }
    }
    .chat-footer-bar {
      height: 120px;
      width: 100%;
      padding: 0 20px 20px;
      border-top: 1px solid #EFEFEF;
      textarea {
        display: inline-block;
        min-height: 50px;
        height: 50px;
        width: 100%;
        margin-bottom: 5px;
        border: none;
        @include input-placeholder {
          color: #888;
        }
      }
      .btn {
        float: right;
        border: none;
        border-radius: 4px;
        background-color: #3899FF;
        color: $white;
      }
    }
  }
}
