/**
 * Base SCSS
 */

* {
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke: 0;
  @include css3-prefix(text-shadow, none);
}

body {
  margin: 0;
  color: $base-font-color;
  font-family: 'Lato', sans-serif;
}

ul,
ol {
  margin: 0;
  padding: 0;
  li {
    list-style: none;
  }
}

a {
  text-decoration: none;
  &:hover,
  &:focus {
    text-decoration: none;
  }
}

img {
  max-width: 100%;
}


