/**
 * Header SCSS
 */


body {
  padding-top: 92px;
  @media (max-width: 767px) {
    padding-top: 74px;
  }
}

header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  display: inline-block;
  width: 100%;
  padding: 40px 50px;
  background-color: $black;
  @media (max-width: 767px) {
    padding: 15px;
  }
  .brand-name {
    display: inline-block;
    width: 115px;
    margin: -20px 0;
    @media (max-width: 767px) {
    	width: 60px;
      margin: 0;
      margin-right: 8px;
    }
  }
  .tagline {
    display: inline-block;
    @include css3-sufix(width, calc(100% - 345px));
    text-align: center;
    /*@media (max-width: 992px) {
      @include css3-sufix(width, calc(100% - 440px));
    }*/
    @media (max-width: 767px) {
      display: none;
    }
    img {
      max-height: 70px;
      animation: InOut 2s infinite alternate;
    }
  }
  @keyframes InOut {
    from {
      @include css3-prefix(transform, scale(.6));
    }
    to {
      @include css3-prefix(transform, scale(1));
    }
  }
  .toggle-menu {
    float: right;
    position: relative;
    z-index: 999;
    display: inline-block;
    height: 30px;
    width: 30px;
    margin-top: 17px;
    padding: 0;
    vertical-align: middle;
    cursor: pointer;
    @media (max-width: 767px) {
      margin-top: 8px;
    }
    span {
      position: absolute;
      top: 50%;
      display: block;
      height: 2px;
      width: 100%;
      background-color: #333;
      transform: translate(0, -50%);
      transition: opacity .3s .3s;
      .home:not(.open) & {
        background-color: #333;
      }
    }
    &::before,
    &::after {
      position: absolute;
      display: block;
      height: 2px;
      width: 100%;
      background-color: #333;
      content: '';
      transition: transform .3s, top .3s .3s, bottom .3s .3s;
    }
    &::before {
      top: 6px;
    }
    &::after {
      bottom: 6px;
    }
    &.open {
      span {
        opacity: 0;
        transition: opacity .3s;
      }
      &::before {
        top: calc(50% - 1px);
        transform: rotate(45deg);
      }
      &::after {
        bottom: calc(50% - 1px);
        transform: rotate(-45deg);
      }
      &::before,
      &::after {
        transition: top .3s, bottom .3s, transform .3s .3s;
      }
    }
  }
  .right-wap {
    float: right;
    display: inline-block;
    @include css3-sufix(width, calc(100% - 300px));
    margin-top: 15px;
    margin-right: 10px;
    text-align: right;
    @media (max-width: 767px) {
      @include css3-sufix(width, calc(100% - 107px));
      margin-top: 5px;
      margin-right: 8px;
    }
    .main-menu {
      display: inline-block;
      li {
        display: inline-block;
        padding: 0 10px;
        vertical-align: middle;
        @media (max-width: 767px) {
          padding: 0 5px;
          display: none;
          &:last-child {
            display: inline-block;
          }
        }
        a {
          color: $white;
          font-size: calc((1 - 1) * 1.2vw + 1rem);
          padding: 2px 0;
          @media (min-width: 1484px) {
            font-size: calc(1 * 1rem);
          }
          &:not(.btn).active {
            border-bottom: 1px solid $white;
          }
          &.btn {
            padding: 12px 20px;
            border: none;
            border-radius: 30px;
            background-color: #DEA412;
            color: $white;
            text-transform: capitalize;
            font-size: calc((.9 - 1) * 1.2vw + 1rem);
            @media (min-width: 1484px) {
              font-size: calc(.9 * 1rem);
            }
          }
        }
        &:nth-last-child(2) {
          a {
            font-size: 20px;
          }
        }
      }
    }
    .menu {
      display: inline-block;
      vertical-align: middle;
      li {
        display: inline-block;
        padding: 0 10px;
        @media (max-width: 767px) {
          padding: 0 5px;
        }
        a {
          color: $white;
          img {
            width: 26px;
          }
        }
      }
    }
    .profile-wrap {
      position: relative;
      display: inline-block;
      margin: 0 10px;
      vertical-align: middle;
      @media (max-width: 767px) {
        margin: 0 5px;
      }
      > a {
        color: $white;
        img {
          height: 34px;
          width: 34px;
          border: 2px solid #333;
          border-radius: 100%;
          overflow: hidden;
        }
      }
      .profile-option {
        position: absolute;
        @include css3-prefix(top, calc(100% + 10px));
        left: auto;
        right: 0;
        display: none;
        width: 180px;
        @include css3-prefix(box-shadow, 0 0 8px 0 rgba($black, .1));
        ul {
          display: inline-block;
          width: 100%;
          text-align: left;
          li {
            display: inline-block;
            width: 100%;
            border-bottom: 1px solid #DDD;
            a {
              display: inline-block;
              width: 100%;
              padding: 10px 15px;
              background-color: $white;
              color: $base-color;
            }
          }
        }
      }
    }
    .notification-wrap {
      position: relative;
      display: inline-block;
      margin: 0 10px;
      vertical-align: middle;
      @media (max-width: 767px) {
        margin: 0 5px;
      }
      .notification-icon {
        position: relative;
        img {
          width: 26px;
        }
        .noti-bell {
          position: absolute;
          top: 0;
          right: 0;
          display: inline-block;
          width: 20px;
          height: 20px;
          margin: -4px -8px 0 0;
          border-radius: 100%;
          background-color: $white;
          color: $base-color;
          font-size: 10px;
          line-height: 20px;
          text-align: center;
        }
      }
      ul {
        position: absolute;
        @include css3-prefix(top, calc(100% + 10px));
        left: auto;
        right: 0;
        display: none;
        width: 300px;
        background-color: $white;
        @include css3-prefix(box-shadow, 0 0 8px 0 rgba($black, .1));
        text-align: left;
        @media (max-width: 460px) {
          width: 280px;
        }
        li {
          display: inline-block;
          width: 100%;
          padding: 3px 10px;
          border-bottom: 1px solid #DDD;
          a {
            display: inline-block;
            width: 100%;
            padding: 10px 15px;
            background-color: #333;
            color: $base-color;
            font-size: 14px;
          }
        }
      }
    }
  }
}
.login-header {
  header {
    background-color: #01555B;
    .right-wap .profile-wrap>a img {
      border-color: #C1C1C1;
    }
    .brand-name {
      margin: 0;
    }
    .toggle-menu span,
    .toggle-menu:before,
    .toggle-menu:after {
      background-color: #C1C1C1;
    }
  }
}
.sidemenu-main {
  position: fixed;
  top: 80px;
  right: -100%;
  left: auto;
  z-index: 9;
  display: inline-block;
  @include css3-sufix(height, calc(100vh - 80px));
  width: 300px;
  padding: 50px 0;
  overflow: auto;
  background-color: #01555B;
  @include css3-prefix(transition, .5s all);
  @media (max-width: 767px) {
    top: 70px;
    padding: 20px 0;
  }
  &.open {
    right: 0;
  }
  ul {
    display: inline-block;
    width: 100%;
    padding-right: 20px;
    li {
      display: inline-block;
      width: 100%;
      a {
        display: inline-block;
        width: 100%;
        padding: 12px 15px;
        color: $white;
        @include css3-prefix(transition, .2s all);
        border-bottom: 1px solid rgba($white, .3);
      }
      &.active,
      &:hover {
        > a {
        	padding-left: 20px;
	        background-color: $white;
	        color: #01555B;
      	}
      }
      &.submenu-main {
        ul {
          display: none;
          padding-right: 0;
          padding-left: 20px;
        }
        &.active {
        	ul {
        		display: block;
        	}
        }
      }
    }
  }
  .profile-sidebar {
    display: inline-block;
    width: 100%;
    text-align: center;
    .profile-userpic {
      display: inline-block;
      width: 100%;
      margin-bottom: 10px;
      img {
        height: 100px;
        width: 100px;
        border-radius: 100%;
      }
    }
    .profile-usertitle {
      display: inline-block;
      width: 100%;
      margin-bottom: 10px;
      color: $white;
    }
    .profile-userbuttons {
      .btn {
        background-color: $white;
        color: #01555B;
        border: 2px solid $white;
        transition: .2s all;
        &:hovre,
        &:focus {
          background-color: transparent !important;
          color: $white;
        }
      }
    }
  }
}

