/**
 * Dashboard SCSS
 */

body.newdashboard {
  /*@include css3-prefix(transition, .2s all);*/
  padding-top: 151px;
  padding-left: 300px;
  @media (max-width: 767px) {
    padding-top: 113px;
    padding-left: 46px;
  }
  &.sidebar-shrink {
    padding-left: 46px;
  }
}
.new-theme {
  display: inline-block;
  min-height: calc(100vh - 110px);
  padding: 20px;
  background-color: #F0F3F4;
  @media (max-width: 992px) {
    padding: 30px;
  }
  @media (max-width: 767px) {
    padding: 20px 15px;
  }
  &.new-dashboard-page {
    .cancle-membership {
      background-color: red;
      &:hover {
        background-color: #DD0101 !important;
      }
    }
    .accounts-wrap {
      .account-header {
        display: inline-block;
        width: 100%;
        margin-bottom: 20px;
        color: $base-color;
        font-size: 30px;
        text-align: center;
      }
      .account-body {
        display: inline-block;
        width: 100%;
        background-color: $white;
        box-shadow: 0 0 8px 0 rgba($black, .1);
        border-radius: 10px;
        padding: 30px 20px;
        .account {
          float: left;
          display: inline-block;
          @include css3-sufix(width, calc(25% - 15px));
          padding: 30px 15px;
          margin-right: 20px;
          margin-bottom: 25px;
          border: 1px solid rgba($base-color, .17);
          border-radius: 10px;
          box-shadow: inset 0 0 9px 0 rgba($base-color, .2);
          text-align: center;
          &:nth-child(4) {
            margin-right: 0;
          }
          @media (max-width: 1200px) {
            @include css3-sufix(width, calc(50% - 15px));
            &:nth-child(2n) {
              margin-right: 0;
            }
          }
          @media (max-width: 640px) {
            width: 100%;
            margin-right: 0;
          }
          h4 {
            margin-bottom: 10px;
            color: $base-color
          }
          span {
            display: inline-block;
            width: 100%;
          }
          .text {
            display: inline-block;
            width: 100%;
            margin-bottom: 15px;
          }
        }
      }
      .upgrading-plan {
        display: inline-block;
        width: 100%;
        text-align: right;
        a {
          text-align: center;
        }
      }
      &:not(.refer-wrap) {
        .account-body {
          @include css3-sufix(display, flex);
          @include css3-prefix(flex-wrap, wrap);
        }
      }
    }
  }
  .status {
    display: inline-block;
    width: 66px;
    padding: 4px 0;
    border-radius: 3px;
    text-align: center;
    color: $white;
    font-size: 14px;
    vertical-align: middle;
    @media (max-width: 767px) {
      width: 48px;
      font-size: 12px;
    }
    &.done {
      background-color: #C9F7F5;
      color: #1BC5BD;
    }
    &.pending {
      background-color: #FFF4DE;
      color: #FFA800;
    }
    &.in-review {
      background-color: #E1F0FF;
      color: #3699FF;
    }
    &.close {
      background-color: #FFE2E5;
      color: #F64E60;
    }
  }
  .right-link {
    display: inline-block;
    width: 100%;
    vertical-align: middle;
    text-align: right;
    li {
      display: inline-block;
      a {
        display: inline-block;
        padding: 6px 12px;
        border: 1px solid transparent;
        border-radius: 4px;
        color: $primary-color;
        font-size: 10px;
      }
      &.active {
        a {
          background-color: #3699FF;
          color: $white;
        }
      }
    }
  }
  .appointment-info {
    display: inline-block;
    width: 100%;
    border-radius: 10px;
    .top-grids {
      @include css3-sufix(display, flex);
      width: 100%;
      @include css3-prefix(flex-wrap, wrap);
      .top-grid {
        display: inline-block;
        @include css3-sufix(width, calc(50% - 20px));
        margin-right: 25px;
        margin-bottom: 25px;
        padding: 30px 10px;
        background-color: #FFF4DE;
        background-image: url('../images/Curve.png');
        background-repeat: no-repeat;
        background-size: 100% auto;
        background-position: center bottom 5px;
        color: #FFA800;
        border-radius: 15px;
        text-align: center;
        @media (max-width: 767px) {
          @include css3-sufix(width, calc(50% - 12.5px));
          margin-right: 20px;
        }
        &:nth-child(2n) {
          margin-right: 0;
        }
        @media (max-width: 480px) {
          width: 100%;
          margin-right: 0;
        }
        &:first-child {
          * {
            color: #FFA800;
          }
        }
        &:nth-child(2n) {
          background-color: #E1F0FF;
          * {
            color: #3699FF;
          }
        }
        &:nth-child(3n) {
          background-color: #FFE2E5;
          * {
            color: #F64E60;
          }
        }
        &:nth-child(4n) {
          background-color: #C9F7F5;
          * {
            color: #1BC5BD;
          }
        }
        &:nth-child(5n) {
          background-color: #C9F7CF;
          * {
            color: #39AE46;
          }
        }
        a {
          display: inline-block;
          width: 100%;
          font-weight: 600;
          font-size: 16px;
        }
        h3 {
          font-size: 40px;
        }
        .left-content {
          display: inline-block;
          width: 200px;
          padding: 20px;
          vertical-align: middle;
        }
        .right-content {
          display: inline-block;
          @include css3-sufix(width, calc(100% - 205px));
          vertical-align: middle;
        }
      }
    }
    .three-col-grids {
      @include css3-sufix(display, flex);
      width: 100%;
      @include css3-prefix(flex-wrap, wrap);
      .issue-request-wrap,
      .service-list-wrap,
      .three-col-grid {
        display: inline-block;
        background-color: $white;
        border-radius: 5px;
        @include css3-prefix(box-shadow, 0 0 8px 0 rgba($black, .1));
        .heading-wrap {
          display: inline-block;
          width: 100%;
          margin-bottom: 20px;
          padding: 15px 30px;
          background-color: transparent;
          border-bottom: none;
          @media (max-width: 1400px) {
            padding: 15px;
          }
          h3 {
            display: inline-block;
            margin-bottom: 0;
            color: $primary-color;
            font-size: 20px;
            vertical-align: middle;
            @media (max-width: 1400px) {
              font-size: 18px;
            }
            @media (max-width: 767px) {
              font-size: 16px;
            }
            @media (max-width: 480px) {
              font-size: 14px;
            }
            span {
              display: inline-block;
              width: 100%;
              padding: 5px 0;
              border-radius: 5px;
              color: #888;
              font-size: 14px;
              vertical-align: middle;
            }
          }
          .right-btn {
            display: inline-block;
            width: 100px;
            vertical-align: middle;
            @media (max-width: 1400px) {
              width: 75px;
            }
            @media (max-width: 767px) {
              width: 65px;
            }
            a {
              padding: 6px 13px;
              border-radius: 5px;
              background-color: rgba(56, 153, 255, .1);
              color: rgba(56, 153, 255, 1);
              font-size: 14px;
              text-transform: uppercase;
              @media (max-width: 1400px) {
                padding: 6px 7px;
                font-size: 12px;
              }
              @media (max-width: 767px) {
                font-size: 10px;
              }
              &:hover {
                background-color: rgba(56, 153, 255, 1);
                color: $white;
              }
            }
          }
        }
      }
    }
    .three-col-grids {
      .three-col-grid {
        display: inline-block;
        @include css3-sufix(width, calc(33.33% - 16.66px));
        margin-right: 25px;
        margin-bottom: 25px;
        &:nth-child(3n) {
          margin-right: 0;
        }
        @media (max-width: 1600px) {
          @include css3-sufix(width, calc(50% - 12.5px));
          &:nth-child(3n) {
            margin-right: 25px;
          }
          &:nth-child(2n) {
            margin-right: 0;
          }
        }
        @media (max-width: 992px) {
          width: 100%;
          margin-right: 0;
          &:nth-child(3n) {
            margin-right: 0;
          }
        }
        .heading-wrap {
          h3 {
            @include css3-sufix(width, calc(100% - 105px));
            vertical-align: top;
            @media (max-width: 1400px) {
              @include css3-sufix(width, calc(100% - 80px));
            }
            @media (max-width: 767px) {
              @include css3-sufix(width, calc(100% - 70px));
            }
          }
          .right-btn {
            text-align: right;
            a.btn {
              text-align: center;
            }
            a.dot-menu {
              display: inline-block;
              width: 12px;
              padding: 6px 4px;
              background-color: transparent;
              span {
                float: left;
                display: inline-block;
                height: 4px;
                width: 4px;
                border-radius: 100%;
                margin-bottom: 4px;
                background-color: $secoundry-color;
                &:last-child {
                  margin-bottom: 0;
                }
              }
            }
          }
        }
        .visitor-listing,
        .Notification-listing,
        .message-listing {
          display: inline-block;
          width: 100%;
          padding: 0 30px 30px;
          @media (max-width: 1400px) {
            padding: 0 15px 15px;
          }
        }
        .visitor-listing {
          ul {
            li {
              display: inline-block;
              width: 100%;
              margin-bottom: 20px;
              .pro-img {
                display: inline-block;
                width: 50px;
                margin-right: 20px;
                vertical-align: middle;
                @media (max-width: 1400px) {
                  margin-right: 10px;
                }
                img {
                  height: 50px;
                  width: 50px;
                  border-radius: 5px;
                  background-color: $base-color;
                }
              }
              .pro-info {
                display: inline-block;
                @include css3-sufix(width, calc(100% - 84px));
                vertical-align: middle;
                @media (max-width: 767px) {
                  @include css3-sufix(width, calc(100% - 74px));
                }
                h4 {
                  float: left;
                  display: inline-block;
                  width: 100%;
                  margin-bottom: 0;
                  color: #333;
                  font-size: 14px;
                  font-weight: 600;
                }
                span {
                  float: left;
                  display: inline-block;
                  width: 100%;
                  color: #999;
                  font-size: 14px;
                  @media (max-width: 767px) {
                    font-size: 10px;
                  }
                  a {
                    color: #999;
                  }
                }
              }
              .day-data {
                display: inline-block;
                width: 80px;
                color: #999;
                font-size: 12px;
                vertical-align: text-bottom;
                text-align: right;
                @media (max-width: 767px) {
                  width: 70px;
                }
              }
            }
          }
        }
        .Notification-listing {
          ul {
            li {
              position: relative;
              display: inline-block;
              width: 100%;
              margin-bottom: 20px;
              &::before {
                position: absolute;
                top: 30px;
                left: 9px;
                display: inline-block;
                @include css3-sufix(height, calc(100% - 10px));
                width: 2px;
                background-color: #D1D1D1;
                content: '';
              }
              &:last-child {
                &::before {
                  display: none;
                }
              }
              .checked {
                position: relative;
                display: inline-block;
                width: 30px;
                margin-top: 5px;
                margin-right: 20px;
                vertical-align: top;
                @media (max-width: 1400px) {
                  margin-right: 0;
                }
                &::before {
                  display: inline-block;
                  height: 20px;
                  width: 20px;
                  border: 5px solid #FFA800;
                  border-radius: 100%;
                  content: '';
                }
                
              }
              &:nth-child(2n) {
                .checked::before {
                  border-color: #1BC5BD;
                }
              }
              &:nth-child(3n) {
                .checked::before {
                  border-color: #F64E60;
                }
              }
              .notification {
                display: inline-block;
                @include css3-sufix(width, calc(100% - 55px));
                padding-bottom: 20px;
                border-bottom: 1px solid #D1D1D1;
                vertical-align: top;
                @media (max-width: 1400px) {
                  @include css3-sufix(width, calc(100% - 35px));
                }
                h4 {
                  display: inline-block;
                  margin-bottom: 0;
                }
                a {
                  color: #333;
                  font-size: 14px;
                  font-weight: 600;
                }
                span {
                  display: inline-block;
                  color: #999;
                  font-size: 14px;
                }
                .day-data {
                  display: inline-block;
                  width: 100%;
                  color: #999;
                  font-size: 14px;
                }
                .btn-wrap {
                  display: inline-block;
                  width: 100%;
                  margin: 10px 0;
                  .poll {
                    padding: 6px 15px;
                    border-radius: 5px;
                    background-color: #48A0DC;
                    color: $white;
                    font-size: 14px;
                    text-transform: uppercase;
                  }
                }
              }
              &:last-child {
                .notification {
                  border-bottom: none;
                }
              }
            }
          }
        }
        .message-listing {
          .no-msg {
            display: inline-block;
            width: 100%;
            margin-bottom: 20px;
            padding: 5px 15px;
            border-radius: 5px;
            background-color: rgba(195, 74, 74, .2);
            color: rgb(195, 74, 74);
          }
          ul {
            li {
              display: inline-block;
              width: 100%;
              margin-bottom: 20px;
              .pro-img {
                display: inline-block;
                width: 50px;
                margin-right: 20px;
                vertical-align: middle;
                @media (max-width: 1400px) {
                  margin-right: 10px;
                }
                img {
                  height: 50px;
                  width: 50px;
                  background-color: $base-color;
                  border-radius: 5px;
                }
              }
              .pro-info {
                display: inline-block;
                @include css3-sufix(width, calc(100% - 84px));
                vertical-align: middle;
                h4 {
                  display: inline-block;
                  width: 100%;
                  margin-bottom: 0;
                  color: #333;
                  font-size: 13px;
                  font-weight: 600;
                }
                span {
                  display: inline-block;
                  width: 100%;
                  color: #999;
                  font-size: 12px;
                  a {
                    color: #999;
                  }
                }
              }
            }
          }
        }
        .service-listing {
          ul {
            li {
              .pro-icon {
                display: inline-block;
                height: 50px;
                width: 50px;
                margin-right: 20px;
                border-radius: 5px;
                background-color: #E1F0FF;
                color: #3699FF;
                font-size: 28px;
                line-height: 50px;
                text-align: center;
                @media (max-width: 767px) {
                  display: none;
                }
              }
              .pro-info {
                @include css3-sufix(width, calc(100% - 143px));
                @media (max-width: 767px) {
                  @include css3-sufix(width, calc(100% - 60px));
                }
              }
            }
          }
        }
        &.issue-request-wrap {
          ul {
            li {
              .pro-img {
                @media (max-width: 767px) {
                  width: 25px;
                  img {
                    height: 25px;
                    width: 25px;
                  }
                }
              }
              .pro-info {
                @include css3-sufix(width, calc(100% - 143px));
                @media (max-width: 767px) {
                  @include css3-sufix(width, calc(100% - 90px));
                  h4 {
                    font-size: 12px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

/* Profile Edit page */
.newdashboard {
  .profile-edit-page {
    .card-header  {
      background-color: #1E1E2D;
      color: $white;
      .btn {
        background-color: #3899FF;
        color: $white;
        border: none;
        &:hover {
          background-color: #3899FF !important;
          outline: none;
        }
      }
    }
  }
  ul.pagination li.page-item span,
  ul.pagination li.page-item a.page-link {
    border-color: #1E1E2D;
    color: #1E1E2D;
  }
  ul.pagination li.page-item a.page-link:hover,
  ul.pagination li.page-item.active a,
  ul.pagination li.page-item.active span {
    border-color: #1E1E2D;
    background-color: #1E1E2D;
    color: $white;
  }
  .btn-danger,
  .btn-danger:hover {
    border-color: #1E1E2D !important;
    background-color: #1E1E2D !important;
  }
}


.newdashboard {
  .notification-page {
    .account-body {
      .bill {
        .btn {
          color: #1E1E2D;
        }
      }
    }
  }
}

