/**
 * Feed SCSS
 */

.feed-page-main {
  .card-header {
    background-color: #1E1E2D;
    color: $white;
    margin-bottom: 10px;
    h3 {
      color: $white;
    }
  }
}
.feed-main-wrap {
  display: inline-block;
  width: 100%;
  .feed-wrap {
    float: left;
    display: inline-block;
    width: 100%;
    padding: 40px;
    border-radius: 30px;
    background-color: rgba($black, .05);
    @media (max-width: 992px) {
      width: 100%;
    }
    @media (max-width: 767px) {
      padding: 0;
      background-color: transparent;
    }
    .create-post-wrap {
      display: inline-block;
      width: 100%;
      margin-bottom: 20px;
      overflow: hidden;
      border: 1px solid #C7C6C6;
      border-radius: 16px;
      background-color: $white;
      @include css3-prefix(box-shadow, 2px 2px 10px 0 rgba($black, .1));
      h2 {
        display: inline-block;
        width: 100%;
        margin-bottom: 0;
        padding: 10px 20px;
        border-bottom: 1px solid #C7C6C6;
        border-radius: 5px 5px 0 0;
        background-color: #F3F3F3;
        color: #333;
        font-size: 24px;
      }
      .write-post {
        display: inline-block;
        width: 100%;
        padding: 10px 15px;
        .concierge-list {
          li {
            display: inline-block;
            margin-right: 10px;
            span {
              font-size: 14px;
            }
          }
        }
        .pro_img {
          display: inline-block;
          width: 70px;
          vertical-align: top;
          img {
            height: 50px;
            width: 50px;
            border-radius: 100%;
          }
        }
        form {
          display: inline-block;
          @include css3-sufix(width, calc(100% - 80px));
        }
        textarea {
          padding: 5px;
          border: none;
          background-color: transparent;
          line-height: 26px;
        }
        .post_image_div {
          display: inline-block;
          width: 100%;
          margin-bottom: 30px;
          img {
            display: block;
            margin: 0 auto;
          }
        }
      }
      .btn-wrap {
        position: relative;
        display: inline-block;
        width: 100%;
        padding: 15px 0;
        text-align: right;
        &::before {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 1px;
          background-color: #C7C6C6;
          content: '';
        }
        .btn {
          float: right;
          width: 100px;
        }
      }
      .custom-file-control {
        margin-right: 10px;
      }
    }
    .feed-post {
      display: inline-block;
      width: 100%;
      margin-bottom: 20px;
      padding: 20px;
      border: 1px solid #C7C6C6;
      border-radius: 16px;
      background-color: $white;
      @include css3-prefix(box-shadow, 2px 2px 10px 0 rgba($black, .1));
      &.liked {
        border: 2px solid #3699FF;
      }
      &:last-child {
        margin-bottom: 0;
      }
      @media (max-width: 767px) {
        margin-bottom: 15px;
        padding: 15px;
      }
      .pro-detail {
        position: relative;
        display: inline-block;
        width: 100%;
        .pro_img {
          display: inline-block;
          width: 60px;
          vertical-align: top;
          img {
            height: 50px;
            width: 50px;
            border-radius: 100%;
          }
        }
        .pro_name {
          display: inline-block;
          @include css3-sufix(width, calc(100% - 70px));
          padding-right: 40px;
          h3 {
            display: inline-block;
            margin-bottom: 0;
            color: #333;
            font-size: 16px;
            text-transform: capitalize;
          }
          .post-info {
            display: inline-block;
            font-size: 12px;
          }
          .desig,
          .date-time {
            float: left;
            display: inline-block;
            width: 100%;
            font-size: 12px;
          }
          .desig {
            color: #333;
          }
        }
      }
      .open-action-box {
        position: absolute;
        top: -10px;
        right: 4px;
        cursor: pointer;
        span {
          font-size: 22px;
        }
        .action-box {
          position: absolute;
          right: 0;
          text-align: left;
          @include css3-prefix(box-shadow, 0 0 5px 0 rgba($black, .5));
          border-radius: 3px;
          background-color: $white;
          a {
            display: inline-block;
            width: 100%;
            margin-right: 0;
            padding: 6px 12px;
            color: $base-color;
          }
        }
      }
      .post_wrap {
        display: inline-block;
        width: 100%;
        padding: 15px 0;
        text-align: center;
        p {
          margin-bottom: 5px;
          font-size: 16px;
          line-height: 25px;
          text-align: left;
          &:empty {
            display: none;
          }
        }
        a {
          color: $base-color;
          &:hover {
            color: $base-color;
            text-decoration: underline;
          }
        }
        &.profile_pic {
          display: inline-block;
          width: 100%;
          text-align: center;
          img {
            display: inline-block;
            height: 200px;
            width: 200px;
            border: 5px solid #DCD9D9;
            border-radius: 100%;
          }
        }
        .double-img {
          @include css3-sufix(display, flex);
          @include css3-prefix(flex-wrap, wrap);
          @include css3-prefix(align-items, flex-start);
          @include css3-prefix(justify-content, space-between);
          width: 100%;
          .fancybox {
            @include css3-sufix(width, calc(50% - 5px));
            img {
              width: 100%;
            }
          }
        }
        .multiple-img {
          @include css3-sufix(display, flex);
          @include css3-prefix(flex-wrap, wrap);
          @include css3-prefix(align-items, flex-start);
          width: 100%;
          .fancybox {
            @include css3-sufix(width, calc(20% - 8px));
            margin-right: 10px;
            margin-bottom: 10px;
            img {
              width: 100%;
            }
            &:first-child {
              width: 100%;
            }
            &:nth-child(5n + 1) {
              margin-right: 0;
            }
          }
        }
      }
      .like_info {
        display: inline-block;
        width: 100%;
        padding: 10px 0;
        border-top: 1px solid #C7C6C6;
        border-bottom: 1px solid #C7C6C6;
        .like-comment {
          display: inline-block;
          margin-right: 15px;
          .icon {
            display: inline-block;
            height: 30px;
            width: 30px;
            border-radius: 100%;
            background-color: #1066D0;
            color: $white;
            line-height: 30px;
            text-align: center;
          }
        }
      }
      .like_here {
        display: inline-block;
        width: 100%;
        padding: 10px 0;
        .like-comment {
          display: inline-block;
          margin-right: 15px;
          .icon {
            color: #3699FF;
          }
        }
      }
      .comment_wrap {
        display: none;
        .btn-wrap {
          text-align: right;
        }
      }
    }
  }
}
#preview_gallery {
  display: inline-block;
  width: 100%;
  margin: 10px 0;
  ul {
    li {
      list-style: disc;
      list-style-position: inside;
    }
  }
  .pip {
    position: relative;
    display: inline-block;
    width: 18%;
    margin-right: 2%;
    margin-bottom: 2%;
    vertical-align: top;
    img {
      width: 100%;
    }
    .remove {
      position: absolute;
      top: 0;
      right: 0;
      display: inline-block;
      width: 30px;
      height: 30px;
      text-align: center;
      line-height: 30px;
      font-size: 14px;
      background-color: rgba($black, .8);
      color: $white;
    }
  }
}
.comment-wrapper {
  .panel-info {
    .panel-body {
      .media {
        position: relative;
        display: inline-block;
        width: 100%;
        margin-bottom: 10px;
        a {
          width: 50px;
          margin-right: 10px;
          vertical-align: top;
          img {
            width: 50px;
            height: 50px;
            border-radius: 100%;
          }
        }
        .media-body {
          display: inline-block;
          @include css3-sufix(width, calc(100% - 65px));
          .text-muted {
            display: inline-block;
            width: 100%;
          }
          p {
            margin-bottom: 0;
            font-size: 14px;
          }
        }
        textarea {
          line-height: 26px;
        }
      }
    }
  }
}
#upload_status_form_demo {
  #image_div_image {
    > div {
      display: inline-block;
      position: relative;
      @include css3-sufix(width, calc(33.33% - 7px));
      margin-right: 10px;
      margin-bottom: 10px;
      img {
        width: 100%;
      }
      &:nth-child(3n) {
        margin-right: 0;
      }
    }
    .deleteImage {
      position: absolute;
      top: 0;
      right: 0;
      height: 26px;
      width: 26px;
      background-color: rgba($white, .5);
      line-height: 26px;
      text-align: center;
    }
  }
  .btn-wrap {
    text-align: right;
    .custom-file {
      float: left;
    }
  }
}