/**
* Form design
*/


input[type='text'],
input[type='email'],
input[type='password'],
input[type='number'],
input[type='tel'],
input[type='time'],
textarea,
select,
.resizable-textarea textarea,
.form-control {
  height: 50px;
  width: 100%;
  padding: 10px;
  border: 1px solid $border-grey;
  border-radius: 4px;
  background-color: $white;
  color: #333;
  font-size: 15px;
  font-weight: 400;
  @include css3-prefix(box-sizing, border-box);
  @include css3-prefix(box-shadow, none);
  @include input-placeholder {
    color: #333;
  }
  &:focus {
    outline: none;
    &:not([readonly]) {
      border: 1px solid $border-grey;
      background-color: $white;
      outline: none;
      @include css3-prefix(box-shadow, none);
    }
  }
}
label {
  font-size: 15px;
  font-weight: 700;
}
.form-item {
  select {
    padding: 5px 25px 5px 10px;
    @include css3-prefix(appearance, none);
    background-image: url('../images/arrow-down.svg');
    background-position: center right 10px;
    background-repeat: no-repeat;
    background-size: 10px auto;
  }
}
textarea {
  min-height: 110px;
  width: 100%;
}
.btn {
  position: relative;
  padding: 10px;
  background-color: transparent;
  border: 1px solid #333;
  border-radius: 0;
  color: #333;
  white-space: normal;
  font-size: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  @include css3-prefix('transition' , 'all .2s ease 0s');
  &:active,
  &:focus,
  &:hover {
    background-color: $secoundry-color !important;
    color: $white !important;
    outline: none;
  }
  @media (max-width: 767px) {
    letter-spacing: 0;
  }
}
.btn-danger {
  border-color: #DC3545;
  background-color: #DC3545;
  color: $white;
  &:active,
  &:focus,
  &:hover {
    border-color: #DC3545 !important;
    background-color: #DC3545 !important;
    color: $white;
  }
}

.btn-round {
  min-width: 200px;
  padding: 7px 30px;
  background-color: $base-color;
  color: $white;
  font-size: 15px;
  line-height: 20px;
  letter-spacing: 2px;
  text-transform: uppercase;
  @include css3-prefix(border-radius, 20px);
  &:hover {
    background-color: $base-color;
    color: $white;
  }
}
button {
  &:focus {
    outline: none;
  }
}
.btn-border {
  width: auto;
  border: 1px solid $primary-color;
  background-color: transparent;
  color: $primary-color !important;
  &:hover {
    background-color: $primary-color;
    color: $black !important;
  }
}
.btn-lg {
  min-width: 175px;
  padding: 10px 30px;
  font-size: 24px;
  font-weight: 500;
  @media (min-width: 1600px) {
    font-size: 22px;
  }
  @media (max-width: 767px) {
    padding: 6px 15px;
    font-size: 20px;
  }
}

.btn-small {
  min-width: 0;
  padding: 8px 20px;
  border-radius: 4px;
  font-size: 15px;
  font-weight: 500;
  letter-spacing: 1px;
  @media (max-width: 767px) {
    letter-spacing: 0;
  }
}

.btn-icon {
  img {
    display: inline-block;
    width: 20px;
    margin-right: 8px;
    vertical-align: sub;
  }
}
input[type="file"] {
  max-width: 100%;
}
.input-rc {
  position: relative;
  display: inline-block;
  margin: 0;
  font-size: 16px;
  vertical-align: middle;
  input[type='checkbox'] {
    position: absolute;
    top: 0;
    left: 0;
    height: 26px;
    width: 24px;
    opacity: 0;
    + .input-rc-span {
      float: left;
      display: inline-block;
      height: 26px;
      width: 24px;
      margin: 3px 8px 0 0;
      cursor: pointer;
      &::after {
        display: inline-block;
        height: 100%;
        width: 100%;
        margin-top: -2px;
        background-image: url('../images/checkbox.png');
        background-repeat: no-repeat;
        background-size: 100% auto;
        cursor: pointer;
        content: '';
      }
    }
    &:checked {
      + .input-rc-span {
        &::after {
          background-image: url('../images/checkbox-check.png');
        }
      }
    }
  }
  ~ label {
    vertical-align: middle;
  }
}

/* Input type number custom design */
.quantity {
  position: relative;
  display: inline-block;
  width: 100px;
  .quantity-nav {
    .quantity-button {
      display: inline-block;
      height: 40px;
      width: 30px;
      border: 1px solid $border-grey;
      background-color: #F8F8F8;
      line-height: 40px;
      text-align: center;
      cursor: pointer;
      &.quantity-up {
        float: right;
      }
    }
  }
  input {
    position: absolute;
    top: 0;
    left: 30px;
    height: 40px;
    padding: 2px 5px;
    border: 1px solid $border-grey;
    border-right: none;
    border-left: none;
    @include css3-sufix(width, calc(100% - 60px));
    text-align: center;
    &:focus {
      outline: none;
    }
    &::-webkit-inner-spin-button,
    &::-webkit-outer-spin-button {
      @include css3-prefix(appearance, none);
    }
  }
}
/* End Input type number custom design */


.custom-file {
  position: relative;
  height: auto;
  width: auto;
  .custom-file-control {
    position: absolute;
    top: 0;
    left: 0;
    padding: 14px 28px;
    border: none;
    border-radius: 4px;
    background-color: $base-color;
    color: $white;
    font-size: 14px;
    line-height: 14px;
    font-weight: 500;
    cursor: pointer;
    @include css3-prefix('transition' , 'all .3s ease 0s');
    &:hover {
      background-color: $base-color;
      color: $white;
    }
  }
  .custom-file-input {
    width: 140px;
  }
}



.switch {
  .input-rc {
    position: relative;
    display: inline-block;
    input[type='checkbox'],
    input[type='radio'] {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      height: 22px;
      width: 40px;
      cursor: pointer;
      opacity: 0;
      +.input-rc-span {
        position: relative;
        display: inline-block;
        height: 16px;
        width: 40px;
        border-radius: 15px;
        background-color: #EFF3F6;
        cursor: pointer;
        @include css3-prefix(transition, .3s all);
        &::after {
          position: absolute;
          top: 0;
          left: 0;
          display: inline-block;
          height: 20px;
          width: 20px;
          margin-top: -2px;
          margin-left: 0;
          border-radius: 100%;
          background-color: $white;
          background-image: none;
          @include css3-prefix(box-shadow, 0 0 4px 0 rgba(0, 0, 0, .2));
          cursor: pointer;
          content: '';
          @include css3-prefix(transition, .3s all);
        }
      }
      &:checked +.input-rc-span {
        background-color: rgba($base-color, .6);
        &::after {
          margin-left: 20px;
          background-color: $base-color;
          background-image: none;
        }
      }
    }
  }
}