/**
 * Home SCSS
 */


.home-page {
  .banner-wrap {
    position: relative;
    display: flex;
    align-items: center;
    min-height: calc(100vh - 180px);
    width: 100%;
    padding: 100px 0;
    background-size: cover;
    background-repeat: no-repeat;
    .img-wrap {
      height: 0;
      width: 0;
      opacity: 0;
    }
    &::after {
      position: absolute;
      top: 0;
      left: 0;
      display: inline-block;
      width: 100%;
      height: 100%;
      background-color: rgba($black, .6);
      content: '';
    }
    .search-wrap {
      display: inline-block;
      width: 100%;
      padding: 150px 0;
      @media (max-width: 992px) {
        padding: 80px 0;
      }
      @media (max-width: 767px) {
        padding: 50px 0;
      }
      h1 {
        color: $white;
        span {
          clear: left;
          float: left;
          display: inline-block;
          margin-bottom: 10px;
          padding: 0 20px;
          background-color: #06292F;
          font-size: 50px;
          line-height: 70px;
          @media (max-width: 767px) {
            font-size: 20px;
            line-height: 40px;
          }
          &:last-child {
            background-color: #025D6F;
          }
        }
      }
    }
  }
  .about-wrap-main {
    display: inline-block;
    width: 100%;
    padding: 150px 0;
    background-color: #F8F8F8;
    text-align: center;
    @media (max-width: 992px) {
      padding: 50px 0;
    }
    @media (max-width: 767px) {
      padding: 30px 0;
    }
    h2 {
      margin-bottom: 30px;
    }
    p {
      font-size: 16px;
      line-height: 26px;
    }
    .btn-wrap {
      display: inline-block;
      width: 100%;
      margin-top: 40px;
      @media (max-width: 992px) {
        margin-top: 20px;
      }
      .btn {
        background-color: transparent;
        color: #333;
        border: 1px solid #333;
        border-radius: 0;
        padding: 10px 20px;
        font-size: 12px;
        letter-spacing: 2px;
        text-transform: uppercase;
      }
    }
  }
  .zigzag-patern-wrap-main {
    display: inline-block;
    width: 100%;
    .zigzag-patern-wrap {
      @include css3-sufix(display, flex);
      @include css3-prefix(flex-wrap, wrap);
      @include css3-prefix(align-items, center);
      width: 100%;
      .left-content {
        display: inline-block;
        width: 50%;
        padding: 50px 100px;
        text-align: center;
        @media (max-width: 992px) {
          padding: 20px 30px;
        }
        @media (max-width: 767px) {
          width: 100%;
          padding: 20px;
        }
        h2 {
          margin-bottom: 20px;
          font-weight: 600;
          @media (max-width: 992px) {
            font-size: 24px;
          }
        }
        p {
          font-size: 16px;
          line-height: 26px;
        }
        .btn-wrap {
          display: inline-block;
          width: 100%;
          margin-top: 20px;
        }
      }
      .right-content {
        display: inline-block;
        width: 50%;
        @media (max-width: 767px) {
          width: 100%;
        }
        img {
          width: 100%;
        }
      }
      @media (min-width: 768px) {
        &:nth-child(2n) {
          @include css3-prefix(flex-flow, row-reverse);
        }
      }
    }
  }
  .testimonial-wrap {
    display: inline-block;
    width: 100%;
    padding: 100px 0;
    background-color: #F8F8F8;
    @media (max-width: 992px) {
      padding: 50px 0;
    }
    @media (max-width: 767px) {
      padding: 30px 0;
    }
    .container {
      @include css3-sufix(display, flex);
      @include css3-prefix(flex-wrap, wrap);
    }
    .testimonial {
      display: inline-block;
      @include css3-sufix(width, calc(50% - 50px));
      margin-right: 100px;
      padding: 40px 30px;
      border: 2px dashed #C7C3C3;
      text-align: center;
      &:nth-child(2n) {
        margin-right: 0;
      }
      @media (max-width: 992px) {
        @include css3-sufix(width, calc(50% - 25px));
        margin-right: 50px;
        padding: 20px;
      }
      @media (max-width: 640px) {
        width: 100%;
        margin-right: 0;
        margin-bottom: 20px;
        padding: 20px 15px;
      }
      img {
        height: 80px;
        width: 80px;
        margin-bottom: 20px;
        border-radius: 100%;
      }
      p {
        font-size: 16px;
        line-height: 26px;
      }
      h3 {
        display: inline-block;
        width: 100%;
        font-size: 16px;
        font-weight: 600;
      }
    } 
  }
  .virtual-app-tour-wrap {
    display: inline-block;
    width: 100%;
    padding: 80px 0;
    background-color: #FF6062;
    background-image: url('../images/pattern.png');
    background-size: cover;
    @media (max-width: 992px) {
      padding: 40px 0;
    }
    @media (max-width: 767px) {
      padding: 20px 0; 
    }
    .left-content {
      display: inline-block;
      width: 350px;
      vertical-align: middle;
      @media (max-width: 992px) {
        width: 250px;
      }
      @media (max-width: 420px) {
        width: 100%;
        text-align: center;
      }
      img {
        width: 250px;
        @media (max-width: 992px) {
          width: 200px;
        }
        @media (max-width: 420px) {
          width: auto;
        }
      }
    }
    .right-content {
      display: inline-block;
      @include css3-sufix(width, calc(100% - 355px));
      vertical-align: middle;
      @media (max-width: 992px) {
        @include css3-sufix(width, calc(100% - 255px));
      }
      @media (max-width: 420px) {
        width: 100%;
      }
      h2 {
        display: inline-block;
        width: 100%;
        margin-bottom: 20px;
        color: $white;
        font-size: 30px;
        @media (max-width: 992px) {
          font-size: 24px;
        }
      }
      .btn-wrap {
        .btn {
          border-color: $white;
          background-color: $white;
          color: #FF6062;
          &:hover,
          &:focus,
          &:active {
            background-color: #FF6062 !important;
          }
        }
      }
    }
  }
}

.login-wrap {
  position: relative;
  z-index: 1;
  max-width: 500px;
  margin: 0 auto;
  background-color: $white;
  border-radius: 15px;
  padding: 30px;
  h2 {
    display: inline-block;
    width: 100%;
    margin: 0 0 20px;
    color: $base-color;
    font-size: 24px;
    font-weight: 600;
    letter-spacing: 4px;
    text-align: center;
    text-transform: uppercase;
    @media (max-width: 767px) {
      margin: 0 0 10px;
      font-size: 20px;
    }
  }
  label {
    margin-bottom: 5px;
    font-size: 16px;
    font-weight: 500;
  }
  .form-item {
    display: inline-block;
    width: 100%;
    margin-bottom: 25px;
    label {
      display: inline-block;
      width: 100%;
      margin-bottom: 5px;
      font-size: 16px;
      font-weight: 500;
    }
    input,
    select {
      height: 50px;
      border-radius: 30px;
    }
    .form-label {
      position: relative;
      display: inline-block;
      width: auto;
      margin-right: 10px;
      margin-bottom: 10px;
      font-weight: 400;
      span {
        display: inline-block;
        padding: 12px 20px;
        overflow: hidden;
        border: none;
        border-radius: 15px;
        background-color: $primary-color;
        color: $white;
        font-size: 14px;
      }
      input[type="radio"] {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        vertical-align: middle;
        opacity: 0;
        &:checked {
          + span {
            background-color: $base-color;
          }
        }
      }
    }

    a {
      color: $primary-color;
      /*text-decoration: underline;*/
    }
  }
  .form-action {
    @include css3-sufix(display, inline-flex);
    @include css3-prefix(flex-wrap, wrap);
    @include css3-prefix(align-items, center);
    @include css3-prefix(justify-content, space-between);
    @include css3-prefix(flex-direction, row-reverse);
    width: 100%;
    .btn {
      min-width: 162px;
      margin-bottom: 10px;
      border-radius: 25px;
      font-size: 14px;
      @media (max-width: 640px) {
        width: 100%;
      }
      &.btn-login {
        background-color: $base-color;  
      }
    }
    span {
      @media (max-width: 640px) {
        display: none;
      }
    }
  }
  .powered {
    display: inline-block;
    width: 100%;
    margin-top: 15px;
    text-align: center;
    a {
      color: $primary-color;
    }
  }
  .login-link {
    display: inline-block;
    width: 100%;
    margin-top: 25px;
    text-align: center;
    a {
      display: inline-block;
      padding-bottom: 3px;
      color: $base-color;
      border-bottom: 1px solid $base-color; 
    }
  }
}


.login-page {
  .login-main-wrap {
    @include css3-sufix(display, flex);
    @include css3-prefix(flex-wrap, wrap);
    width: 100%;
    @include css3-sufix(min-height, calc(100vh - 105px));
    .left-content {
      position: relative;
      @include css3-sufix(display, inline-flex);
      @include css3-sufix(align-items, center);
      @include css3-sufix(justify-content, center);
      width: 55%;
      background-image: url('../images/login-bg.jpeg');
      background-position: center center;
      background-repeat: no-repeat;
      background-size: cover;
      text-align: center;
      @media (max-width: 1366px) {
        width: 50%;
      }
      @media (max-width: 992px) {
        width: 100%;
        padding: 30px;
      }
      &::after {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        height: 100%;
        width: 100%;
        background-color: rgba(130, 110, 49, .9);
        content: '';
      }
      .content {
        position: relative;
        z-index: 2;
        display: inline-block;
        max-width: 550px;
        text-align: left;
      }
      h1 {
        display: inline-block;
        width: 100%;
        margin-bottom: 10px;
        color: $white;
        font-size: 61px;
        font-weight: 700;
        span {
          color: $base-color;
        }
        @media (max-width: 767px) {
          font-size: 40px;
        }
      }
      p {
        color: $white;
        font-size: 20px;
        line-height: 30px;
        font-weight: 600;
        @media (max-width: 767px) {
          font-size: 16px;
          line-height: 28px;
        }
      }
    }
    .right-content {
      @include css3-sufix(display, inline-flex);
      @include css3-sufix(align-items, center);
      @include css3-sufix(justify-content, center);
      width: 45%;
      background-color: $white;
      @media (max-width: 1366px) {
        width: 50%;
      }
      @media (max-width: 992px) {
        width: 100%;
        padding: 30px;
      }
      @media (max-width: 480px) {
        padding: 30px 15px;
      }
      .login-form-wrap {
        display: inline-block;
        min-height: 584px;
        max-width: 500px;
        width: 100%;
        padding: 15px 15px 0;
        border: 1px solid #B1B1B1;
        border-radius: 16px;
        @media (max-width: 992px) {
          min-height: 0;
        }
      }
      .tabs-wrap {
        @include css3-sufix(display, inline-flex);
        @include css3-prefix(justify-content, space-between);
        width: 100%;
        li {
          display: inline-block;
          width: 48%;
          overflow: hidden;
          a {
            display: inline-block;
            width: 100%;
            padding: 5px 30px;
            border: 1px solid #B1B1B1;
            border-radius: 26px;
            color: $secoundry-color;
            font-size: 24px;
            font-weight: 500;
            text-align: center;
            letter-spacing: 4px;
            @include css3-prefix(transition, .2s all);
            &:hover {
              color: $white;
              background-color: #AAA;
            }
            @media (max-width: 480px) {
              padding: 5px 15px;
              font-size: 12px;
            }
          }
          &.active {
            a {
              background-color: #AAA;
              color: $white;
            }
          }
        }
      }
      .tabs-content-wrap {
        display: inline-block;
        width: 100%;
        padding: 30px;
        text-align: center;
        @media (max-width: 480px) {
          padding: 30px 0;
        }
        .tabs-content {
          display: none;
          &.active {
            display: inline-block;
          }
          > span {
            color: #555;
          }
          h2 {
            display: inline-block;
            width: 100%;
            margin: 0 0 50px;
            color: $secoundry-color;
            font-size: 28px;
            letter-spacing: 4px;
            text-transform: uppercase;
            @media (max-width: 767px) {
              margin: 0 0 30px;
              font-size: 24px;
            }
          }
          form {
            display: inline-block;
            width: 100%;
            .form-item {
              display: inline-block;
              width: 100%;
              margin-bottom: 30px;
              label {
                font-size: 14px;
                font-weight: 600;
                text-transform: uppercase;
                @media (max-width: 480px) {
                  font-size: 12px;
                }
              }
              input {
                padding: 4px 30px;
                background-color: transparent;
                border-radius: 34px;
                @include input-placeholder {
                  color: $white;
                }
              }
            }
            .forgot-link {
              width: 100%;
              a {
                color: #D57E9C;
                &:hover {
                  text-decoration: underline;
                }
              }
            }
            .form-check {
              display: inline-block;
              width: 100%;
              margin-bottom: 25px;
              padding-left: 0;
              font-size: 12px;
              font-weight: 600;
              text-transform: uppercase;
            }
            .form-action {
              display: inline-block;
              width: 100%;
              margin-bottom: 25px;
              .btn {
                width: auto;
                padding: 10px 30px;
                background-color: $base-color;
                border-radius: 34px;
                letter-spacing: 2px;

              }
              .reg-btn {
                background-color: $secoundry-color;
              }
            }
            .or-set {
              display: inline-block;
              width: 100%;
              margin: 20px 0;
              color: #333;
              text-align: center;
              vertical-align: middle;
              &::before,
              &::after {
                display: inline-block;
                height: 1px;
                @include css3-prefix(width, calc(50% - 70px));
                margin: 0 20px;
                background-color: #333;
                content: '';
                vertical-align: middle;
              }
            }
          }
        }
        #patient {
          h3 {
            display: inline-block;
            width: 100%;
            margin: 0 0 50px;
            color: $base-color;
            font-size: 20px;
            font-weight: 500;
          }
          a {
            float: left;
            clear: left;
            display: inline-block;
            width: 270px;
            margin-bottom: 30px;
            padding: 10px;
            background-color: #333;
            border: 1px solid $white;
            border-radius: 10px; 
            text-align: center;
            @include css3-prefix(transition, .2s all);
            img {
              display: inline-block;
              max-width: 50px;
            }
            span {
              display: inline-block;
              width: calc(100% - 55px);
              color: $white;
              text-align: center;
              vertical-align: middle;
              font-size: 20px;
              font-weight: 500;
            }
            &:hover {
              background-color: rgba(51, 51, 51, .7);
            }
          }
        }
      }
    }
  }
}


.plan-page {
  .banner-wrap {
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    &::after {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background-color: rgba($black, .55);
      content: '';
    }
  }
  .img-wrap {
    height: 0;
    width: 0;
    opacity: 0;
    img {
      height: 0;
      width: 0;
      opacity: 0;
    }
  }
  .banner-content {
    position: relative;
    z-index: 1;
    display: inline-flex;
    flex-wrap: wrap;
    width: 100%;
    align-items: center;
    padding: 90px 50px;
    justify-content: center;
    @media (max-width: 992px) {
      padding: 50px 20px;
    }
    @media (max-width: 767px) {
      padding: 20px;
    }
  }
  .left-content {
    display: inline-block;
    @include css3-sufix(width, calc(100% - 505px));
    color: $white;
    @media (max-width: 992px) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 30px;
    }
    h1 {
      color: $white;
    }
    p {
      font-size: 16px;
      line-height: 28px;
      font-weight: 500;
    }
    .social-icons {
      display: inline-block;
      width: 100%;
      margin-top: 10px;
      li {
        display: inline-block;
        margin-right: 20px;
        a {
          display: inline-block;
          color: $white;
          font-size: 22px;
        }
      }
    }
    .contact-text-wrap {
      display: inline-block;
      width: 100%;
      margin-bottom: 30px;
    }
    .back-btn-wrap {
      display: inline-block;
      width: 100%;
      a.button {
        color: #FFF;
        &:hover {
          color: #FFF;
        }
      }
    }
  }
  .right-content {
    display: inline-block;
    max-width: 500px;
    width: 100%;
    .form-item-row {
      display: inline-block;
      width: 100%;
      label {
        display: inline-block;
        width: 100%;
        color: $white;
        font-size: 16px;
        font-weight: 500;
      }
      .form-item {
        display: inline-block;
        width: 100%;
        margin-bottom: 30px;
        vertical-align: top;
        input {
          height: 42px;
          padding: 3px 10px;
          border: 1px solid #CCC;
          border-radius: 2px;
          background-color: #FAFAFA;
          color: $black;
          &:focus {
            background-color: $white;
          }
        }
        textarea {
          padding: 3px 10px;
          border: 1px solid #CCC;
          border-radius: 2px;
          background-color: #FAFAFA;
          color: $black;
          &:focus {
            background-color: $white;
          }
        }
      }
      .error {
        color: red;
      }
    }
    .name-field {
      .form-item {
        display: inline-block;
        @include css3-sufix(width, calc(50% - 12px));
        margin-right: 20px;
        &:last-child {
          margin-right: 0;
        }
        @media (max-width: 480px) {
          width: 100%;
          margin-right: 0;
        }
        input {
          margin-bottom: 5px;
        }
        span {
          display: inline-block;
          width: 100%;
          margin-bottom: 8px;
          color: $white;
          font-size: 16px;
        }
      }
    }
    .contact-form-wrap {
      padding: 30px 50px;
      color: #333;
      background-color: $white;
      border-radius: 30px;
      @media (max-width: 767px) {
        border-radius: 20px;
        padding: 30px 15px;
      }
      h2 {
        display: inline-block;
        width: 100%;
        margin: 0 0 30px;
        color: $secoundry-color;
        font-size: 32px;
        font-weight: 700;
        letter-spacing: 1px;
        @media (max-width: 767px) {
          margin: 0 0 20px;
          font-size: 28px;
        }
      }
      .card-symbols {
        display: flex;
        width: 100%;
        align-items: center;
        img {
          width: 80px !important;
        }
      }
      .pay-with {
        display: inline-block;
        width: 100%;
        margin-bottom: 15px;
        span {
          display: inline-block;
          width: 100%;
          margin-bottom: 15px;
        }
      }
      .form-item {
        display: inline-block;
        width: 100%;
        margin-bottom: 20px;
        select,
        input {
          padding: 4px 30px;
          color: $secoundry-color;
          border-radius: 34px;
          @include input-placeholder {
            color: $secoundry-color;
          }
        }
        label {
          width: 100%;
        }
        select {
          min-width: 100px;
          width: auto;
        }
      }
      .form-action {
        .btn {
          width: 100%;
          padding: 15px 20px;
          border-radius: 34px;
          border-color: $white;
          color: $white;
          background-color: $secoundry-color;
          &:hover,
          &:focus {
            background-color: $secoundry-color;
            color: $white;
          }
        }
      }
    }
  }
}


.clock-wrap {
  .ul-btn__icon {
    color: #3699FF;
    margin-right: 10px;
  }
  .btn:focus {
    box-shadow: none;
  }
  .btn:not(.btn-danger) {
    color: $white;
    border-color: #FFA500;
    background-color: #FFA500 !important;
    &:hover {
      color: $white;
      background-color: #FFA500 !important;
    }
  }
  .btn.btn-danger {
    border-color: #3699FF !important;
    background-color: #3699FF !important;
    color: $white;
    &:hover {
      border-color: #3699FF !important;
      background-color: #3699FF !important;
      color: $white;
    }
  }
}
