/**
 * Footer SCSS
 */


footer {
  display: inline-block;
  width: 100%;
  padding: 50px 0;
  background-color: #272725;
  @media (max-width: 767px) {
    padding: 10px 0;
  }
  .footer-menu-wrap {
    display: inline-flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    ul {
      margin: 0;
      padding: 0;
      li {
        display: inline-block;
        padding: 0 10px;
        @media (max-width: 767px) {
          padding: 0 5px;
        }
        a {
          color: $white;
          font-size: 16px;
          &:hover {
            color: #FF6062;
          }
        }
      }
    }
    .instagram-icon {
      display: inline-block;
      width: 48px;
      height: 48px;
      border-radius: 100%;
      background-color: $white;
      text-align: center;
      line-height: 48px;
      a {
        color: #333;
        font-size: 20px;
      }
    }
    .footer-text {
      flex: 1 1 auto;
      color: $white;
      font-size: 35px;
      font-weight: 700;
      text-align: center;
      @media (max-width: 1024px) {
        font-size: 20px;
      }
      @media (max-width: 767px) {
        font-size: 12px;
      }
    }
    .ios,
    .android {
      display: inline-block;
      margin-left: 10px;
      a {
        color: $white;
        font-size: 100px;
        @media (max-width: 1024px) {
          font-size: 50px;
        }
        @media (max-width: 767px) {
          font-size: 30px;
        }
      }
    }

  }
  .copyright {
    display: inline-block;
    width: 395px;
    color: $white;
    font-size: 16px;
    vertical-align: middle;
    text-align: right;
    @media (max-width: 767px) {
      width: 100%;
    }
  }
}

.newdashboard {
  footer {
    padding: 10px 0;
    .footer-menu-wrap .ios a,
    .footer-menu-wrap .android a {
      font-size: 74px;
      @media (max-width: 1024px) {
        font-size: 40px;
      }
      @media (max-width: 767px) {
        font-size: 26px;
      }
    }
  }
}
