/**
 * Components SCSS
 *
 */

.pagination {
  display: inline-block;
  width: 100%;
  margin: 10px 0;
  text-align: right;
  ul {
    li {
      display: inline-block;
      margin-left: 5px;
      span,
      a {
        display: inline-block;
        min-width: 30px;
        padding: 0 10px;
        border: 1px solid $base-color;
        border-radius: 5px;
        color: $base-color;
        line-height: 30px;
        text-align: center;
        @include css3-prefix(transition, .2s all);
        &:hover,
        &.active {
          border-color: $secoundry-color;
          color: $white;
          background-color: $secoundry-color;
        }
      }
    }
  }
}
.dataTables_paginate {
  display: inline-block;
  width: 100%;
  white-space: normal;
}
ul.pagination {
  display: inline-block;
  width: 100%;
  white-space: normal;
  li.page-item {
    display: inline-block;
    margin-left: 5px;
    span,
    a.page-link {
      display: inline-block;
      min-width: 30px;
      padding: 0 10px;
      border: 1px solid $base-color;
      border-radius: 5px;
      color: $base-color;
      line-height: 30px;
      text-align: center;
      @include css3-prefix(transition, .2s all);
      &:hover,
      &.active {
        border-color: $base-color;
        color: $white;
        background-color: $base-color;
      }
    }
    &.active {
      a,
      span {
        border-color: $base-color;
        color: $white;
        background-color: $base-color;
      }
    }
  }
}

.select2 + .select2-container {
  .select2-selection--single {
    height: 50px;
    .select2-selection__rendered {
      line-height: 50px;
    }
    .select2-selection__arrow {
      height: 50px;
    }
  }
}


.login-modal {
  padding-right: 0 !important;
  .modal-content {
    position: relative;
  }
  .close {
    position: absolute;
    top: -12px;
    right: -8px;
    left: auto;
    z-index: 9;
    height: 26px;
    width: 26px;
    border: 2px solid $white;
    background-color: $black;
    border-radius: 100%;
    color: $white;
    font-size: 22px;
    line-height: 23px;
    font-weight: 300;
    opacity: 1;
    &:hover {
      opacity: 1;
      span {
        color: $white;
      }
    }
  }
  .modal-body {
    max-height: 90vh;
    overflow-y: auto;
    padding: 30px;
    h2 {
      display: inline-block;
      width: 100%;
      margin: 0 0 20px;
      color: $base-color;
      font-size: 24px;
      font-weight: 600;
      letter-spacing: 4px;
      text-align: center;
      text-transform: uppercase;
      @media (max-width: 767px) {
        margin: 0 0 10px;
        font-size: 20px;
      }
    }
    label {
      margin-bottom: 5px;
      font-size: 16px;
      font-weight: 500;
    }
    .form-item {
      display: inline-block;
      width: 100%;
      margin-bottom: 25px;
      label {
        display: inline-block;
        width: 100%;
        margin-bottom: 5px;
        font-size: 16px;
        font-weight: 500;
      }
      input,
      select {
        height: 50px;
        border-radius: 30px;
      }
      .form-label {
        position: relative;
        display: inline-block;
        width: auto;
        margin-right: 10px;
        margin-bottom: 10px;
        font-weight: 400;
        span {
          display: inline-block;
          padding: 12px 20px;
          overflow: hidden;
          border: none;
          border-radius: 15px;
          background-color: $primary-color;
          color: $white;
          font-size: 14px;
        }
        input[type="radio"] {
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
          width: 100%;
          vertical-align: middle;
          opacity: 0;
          &:checked {
            + span {
              background-color: $base-color;
            }
          }
        }
      }

      a {
        color: $primary-color;
        /*text-decoration: underline;*/
      }
    }
    .form-action {
      @include css3-sufix(display, inline-flex);
      @include css3-prefix(flex-wrap, wrap);
      @include css3-prefix(align-items, center);
      @include css3-prefix(justify-content, space-between);
      @include css3-prefix(flex-direction, row-reverse);
      width: 100%;
      .btn {
        min-width: 162px;
        margin-bottom: 10px;
        border-radius: 25px;
        font-size: 14px;
        @media (max-width: 640px) {
          width: 100%;
        }
        &.btn-login {
          background-color: $base-color;  
        }
      }
      span {
        @media (max-width: 640px) {
          display: none;
        }
      }
    }
    .powered {
      display: inline-block;
      width: 100%;
      margin-top: 15px;
      text-align: center;
      a {
        color: $primary-color;
      }
    }
    .login-link {
      display: inline-block;
      width: 100%;
      margin-top: 25px;
      text-align: center;
      a {
        display: inline-block;
        padding-bottom: 3px;
        color: $base-color;
        border-bottom: 1px solid $base-color; 
      }
    }
  }
  .col-md-1 {
    @media (max-width: 767px) {
      display: none;
    }
    .form-item {
      font-size: 26px;
      line-height: 50px;
    }
  }
  .go-back {
    display: inline-block;
    margin-bottom: 20px;
    line-height: 26px;
    img {
      margin-right: 5px;
      vertical-align: middle;
    }
  }
}
