/**
 * Dashboard SCSS
 * Package Page SCSS
 * Profile Edit Page SCSS
 */

.dashboard-page {
  display: inline-block;
  min-height: calc(100vh - 110px);
  width: 100%;
  padding: 50px;
  background-color: #F0F3F4;
  @media (max-width: 992px) {
    padding: 30px;
  }
  @media (max-width: 767px) {
    padding: 20px 15px;
  }
  .cancle-membership {
    background-color: red;
    border: none;
    color: $white;
    font-size: 12px;
    text-transform: capitalize;
    &:hover {
      background-color: #DD0101 !important;
    }
  }
  .accounts-wrap {
    .account-header {
      display: inline-block;
      width: 100%;
      margin-bottom: 20px;
      color: $base-color;
      font-size: 30px;
      text-align: center;
    }
    .account-body {
      display: inline-block;
      width: 100%;
      background-color: $white;
      box-shadow: 0 0 8px 0 rgba($black, .1);
      border-radius: 10px;
      padding: 30px 20px;
      .account {
        float: left;
        display: inline-block;
        @include css3-sufix(width, calc(25% - 15px));
        padding: 30px 15px;
        margin-right: 20px;
        margin-bottom: 25px;
        border: 1px solid rgba($base-color, .17);
        border-radius: 10px;
        box-shadow: inset 0 0 9px 0 rgba($base-color, .2);
        text-align: center;
        &:nth-child(4) {
          margin-right: 0;
        }
        @media (max-width: 1200px) {
          @include css3-sufix(width, calc(50% - 15px));
          &:nth-child(2n) {
            margin-right: 0;
          }
        }
        @media (max-width: 640px) {
          width: 100%;
          margin-right: 0;
        }
        h4 {
          margin-bottom: 10px;
          color: $base-color
        }
        span {
          display: inline-block;
          width: 100%;
        }
        .text {
          display: inline-block;
          width: 100%;
          margin-bottom: 15px;
        }
      }
    }
    .upgrading-plan {
      display: inline-block;
      width: 100%;
      text-align: right;
      a {
        text-align: center;
      }
    }
    &:not(.refer-wrap) {
      .account-body {
        @include css3-sufix(display, flex);
        @include css3-prefix(flex-wrap, wrap);
      }
    }
  }
  .appointment-info {
    display: inline-block;
    width: 100%;
    border-radius: 10px;
    h2 {
      display: inline-block;
      width: 100%;
      margin-right: 0;
      color: $primary-color;
      font-size: 26px;
      @media (max-width: 992px) {
        margin-bottom: 0;
        font-size: 18px;
      }
    }
    >.row {
      @include css3-prefix(align-items, center);
      .col-2 {
        @media (max-width: 767px) {
          flex: 0 0 100%;
          max-width: 100%;
          img {
            max-width: 200px;
          }
        }
      }
    }
    #clockbox {
      display: inline-block;
      /*width: 100%;*/
      color: $base-color;
      font-size: 40px;
      font-family: 'Squada One', cursive;
      vertical-align: middle;
      @media (max-width: 767px) {
        width: 100%;
        margin-bottom: 10px;
        font-size: 26px;
      }
    }
    #emergencyAlarm {
      display: inline-block;
      max-width: 100%;
      background-color: transparent !important;
      padding: 0;
      border-radius: 0;
    }
    .top-grids {
      @include css3-sufix(display, flex);
      width: 100%;
      @include css3-prefix(flex-wrap, wrap);
      margin-top: 25px;
      .top-grid {
        display: inline-block;
        @include css3-sufix(width, calc(20% - 20px));
        margin-right: 25px;
        margin-bottom: 25px;
        padding: 30px 10px;
        background-color: #00BCD4;
        background-image: url('../images/Curve.png');
        background-repeat: no-repeat;
        background-size: 100% auto;
        background-position: center bottom 5px;
        color: $white;
        border-radius: 5px;
        text-align: center;
        @media (min-width: 993px) {
          &:nth-child(5n) {
            margin-right: 0;
          }
        }
        @media (max-width: 992px) and (min-width: 768px) {
          @include css3-sufix(width, calc(33.33% - 16.66px));
          &:nth-child(5n) {
            margin-right: 25px;
          }
          &:nth-child(3n) {
            margin-right: 0;
          }
        }
        @media (max-width: 767px) {
          @include css3-sufix(width, calc(50% - 12.5px));
          &:nth-child(2n) {
            margin-right: 0;
          }
        }
        @media (max-width: 480px) {
          width: 100%;
          margin-right: 0;
        }
        &:nth-child(2n) {
          background-color: #8BC34A;
        }
        &:nth-child(3n) {
          background-color: #1E88CF;
        }
        &:nth-child(4n) {
          background-color: #A667DD;
        }
        &:nth-child(5n) {
          background-color: #FFC701;
        }
        a {
          display: inline-block;
          width: 100%;
          color: $white;
        }
        h3 {
          font-size: 40px;
        }
        .left-content {
          display: inline-block;
          width: 200px;
          padding: 20px;
          vertical-align: middle;
        }
        .right-content {
          display: inline-block;
          @include css3-sufix(width, calc(100% - 205px));
          vertical-align: middle;
        }
      }
    }
    .two-col-grids,
    .three-col-grids {
      @include css3-sufix(display, flex);
      width: 100%;
      @include css3-prefix(flex-wrap, wrap);
      .issue-request-wrap,
      .service-list-wrap,
      .three-col-grid {
        display: inline-block;
        background-color: $white;
        border-radius: 5px;
        @include css3-prefix(box-shadow, 0 0 8px 0 rgba($black, .1));
        .heading-wrap {
          display: inline-block;
          width: 100%;
          margin-bottom: 20px;
          padding: 15px 30px;
          background-color: #01555B;
          border-bottom: 1px solid #D1D1D1;
          @media (max-width: 1400px) {
            padding: 15px;
          }
          h3 {
            display: inline-block;
            margin-bottom: 0;
            color: $white;
            font-size: 20px;
            vertical-align: middle;
            @media (max-width: 1400px) {
              font-size: 18px;
            }
            @media (max-width: 767px) {
              font-size: 16px;
            }
            span {
              display: inline-block;
              padding: 5px 10px;
              border-radius: 5px;
              background-color: #48A0DC;
              color: $white;
              font-size: 12px;
              vertical-align: middle;
            }
          }
          .right-btn {
            display: inline-block;
            width: 100px;
            vertical-align: middle;
            @media (max-width: 1400px) {
              width: 75px;
            }
            a {
              padding: 6px 13px;
              border-radius: 5px;
              background-color: #F1DBC8;
              color: $secoundry-color;
              font-size: 14px;
              text-transform: uppercase;
              @media (max-width: 1400px) {
                padding: 6px 7px;
                font-size: 12px;
              }
            }
          }
        }
      }
    }
    .two-col-grids {
      .issue-request-wrap {
        @include css3-sufix(width, calc(60% - 13px));
        margin-right: 25px;
        margin-bottom: 25px;
        @media (max-width: 992px) {
          width: 100%;
          margin-right: 0;
        }
        .heading-wrap {
          h3 {
            display: inline-block;
            vertical-align: middle;
            @include css3-sufix(width, calc(100% - 288px));
            @media (max-width: 480px) {
              width: 100%;
            }
          }
          .right-link {
            display: inline-block;
            width: 180px;
            vertical-align: middle;
            @media (max-width: 480px) {
              @include css3-sufix(width, calc(100% - 80px));
            }
            li {
              display: inline-block;
              a {
                display: inline-block;
                padding: 3px 12px;
                border: 1px solid transparent;
                border-radius: 14px;
                color: $white;
                font-size: 10px;
              }
              &.active {
                a {
                  border: 1px solid $white;
                }
              }
            }
          }
        }
        .issue-listing-wrap {
          display: inline-block;
          width: 100%;
          padding: 0 30px 30px;
          @media (max-width: 1400px) {
            padding: 0 15px 15px;
          }
          ul {
            li {
              display: inline-block;
              width: 100%;
              margin-bottom: 20px;
              .status {
                display: inline-block;
                width: 80px;
                padding: 5px 0;
                border-radius: 3px;
                text-align: center;
                color: $white;
                font-size: 14px;
                vertical-align: middle;
                @media (max-width: 767px) {
                  width: 60px;
                  font-size: 12px;
                }
                &.done {
                  background-color: #8BC34A;
                }
                &.pending {
                  background-color: $base-color;
                }
                &.in-review {
                  background-color: #4A6FC3;
                }
              }
              .pro-img {
                display: inline-block;
                width: 60px;
                height: 60px;
                border-radius: 100%;
                overflow: hidden;
                vertical-align: middle;
                @media (max-width: 767px) {
                  width: 40px;
                  height: 40px;
                }
              }
              .issue-detail {
                display: inline-block;
                @include css3-sufix(width, calc(100% - 148px));
                vertical-align: middle;
                padding-left: 20px;
                @media (max-width: 767px) {
                  @include css3-sufix(width, calc(100% - 110px));
                  padding-left: 10px;
                }
                h4 {
                  display: inline-block;
                  width: 100%;
                  margin-bottom: 0;
                  font-size: 16px;
                  color: #333;
                  @media (max-width: 767px) {
                    font-size: 14px;
                  }
                }
                span {
                  display: inline-block;
                  width: 100%;
                  color: #777;
                  font-size: 14px;
                  @media (max-width: 767px) {
                    font-size: 12px;
                  }
                }
              }
            }
          }
        }
      }
      .service-list-wrap {
        @include css3-sufix(width, calc(40% - 12px));
        margin-bottom: 25px;
        @media (max-width: 992px) {
          width: 100%;
        }
        .heading-wrap {
          h3 {
            @include css3-sufix(width, calc(100% - 105px));
            @media (max-width: 480px) {
              @include css3-sufix(width, calc(100% - 80px));
            }
          }
        }
        .service-listing {
          display: inline-block;
          width: 100%;
          padding: 0 30px 30px;
          @media (max-width: 1400px) {
            padding: 0 15px 15px;
          }
          ul {
            li {
              display: inline-block;
              width: 100%;
              margin-bottom: 20px;
              .pro-img {
                display: inline-block;
                width: 50px;
                margin-right: 20px;
                vertical-align: middle;
                @media (max-width: 1400px) {
                  margin-right: 10px;
                }
                img {
                  height: 50px;
                  width: 50px;
                  border-radius: 100%;
                }
              }
              .pro-info {
                display: inline-block;
                @include css3-sufix(width, calc(100% - 84px));
                vertical-align: middle;
                @media (max-width: 767px) {
                  @include css3-sufix(width, calc(100% - 64px)); 
                }
                h3 {
                  display: inline-block;
                  margin-bottom: 0;
                  color: #333;
                  font-size: 16px;
                  font-weight: 600;
                  @media (max-width: 767px) {
                    font-size: 14px;
                  }
                }
                span {
                  display: inline-block;
                  color: #999;
                  font-size: 16px;
                  @media (max-width: 767px) {
                    font-size: 14px;
                  }
                  a {
                    color: #999;
                  }
                }
              }
              .status {
                float: none;
                display: inline-block;
                width: 80px;
                padding: 5px 0;
                border-radius: 3px;
                text-align: center;
                color: $white;
                font-size: 14px;
                font-weight: 400;
                line-height: normal;
                vertical-align: middle;
                opacity: 1;
                @media (max-width: 767px) {
                  width: 50px;
                }
                &.open {
                  background-color: #8BC34A;
                }
                &.close {
                  background-color: #C34A4A;
                }
              }
            }
          }
        }
      }
    }
    .three-col-grids {
      .three-col-grid {
        display: inline-block;
        @include css3-sufix(width, calc(33.33% - 16.66px));
        margin-right: 25px;
        margin-bottom: 25px;
        &:nth-child(3n) {
          margin-right: 0;
        }
        @media (max-width: 1200px) {
          @include css3-sufix(width, calc(50% - 12.5px));
          &:nth-child(3n) {
            margin-right: 25px;
          }
          &:nth-child(2n) {
            margin-right: 0;
          }
        }
        @media (max-width: 992px) {
          width: 100%;
          margin-right: 0;
        }
        .heading-wrap {
          h3 {
            @include css3-sufix(width, calc(100% - 105px));
            @media (max-width: 1400px) {
              @include css3-sufix(width, calc(100% - 80px));
            }
          }
          .right-btn {
            text-align: right;
            a.btn {
              text-align: center;
            }
            a.dot-menu {
              display: inline-block;
              width: 12px;
              padding: 6px 4px;
              background-color: transparent;
              span {
                float: left;
                display: inline-block;
                height: 4px;
                width: 4px;
                border-radius: 100%;
                margin-bottom: 4px;
                background-color: $secoundry-color;
                &:last-child {
                  margin-bottom: 0;
                }
              }
            }
          }
        }
        .visitor-listing,
        .Notification-listing,
        .message-listing {
          display: inline-block;
          width: 100%;
          padding: 0 30px 30px;
          @media (max-width: 1400px) {
            padding: 0 15px 15px;
          }
        }
        .visitor-listing {
          ul {
            li {
              display: inline-block;
              width: 100%;
              margin-bottom: 20px;
              .pro-img {
                display: inline-block;
                width: 50px;
                margin-right: 20px;
                vertical-align: middle;
                @media (max-width: 1400px) {
                  margin-right: 10px;
                }
                img {
                  height: 50px;
                  width: 50px;
                  border-radius: 100%;
                  background-color: $base-color;
                }
              }
              .pro-info {
                display: inline-block;
                @include css3-sufix(width, calc(100% - 84px));
                vertical-align: middle;
                h4 {
                  display: inline-block;
                  width: 100%;
                  margin-bottom: 0;
                  color: #333;
                  font-size: 14px;
                  font-weight: 600;
                }
                span {
                  display: inline-block;
                  width: 100%;
                  color: #999;
                  font-size: 14px;
                  a {
                    color: #999;
                  }
                }
              }
              .day-data {
                display: inline-block;
                width: 80px;
                color: #999;
                font-size: 12px;
                vertical-align: text-bottom;
              }
            }
          }
        }
        .Notification-listing {
          ul {
            li {
              position: relative;
              display: inline-block;
              width: 100%;
              margin-bottom: 20px;
              &::before {
                position: absolute;
                top: 20px;
                left: 10px;
                display: inline-block;
                @include css3-sufix(height, calc(100% + 5px));
                width: 1px;
                background-color: #D1D1D1;
                content: '';
              }
              &:last-child {
                &::before {
                  display: none;
                }
              }
              .checked {
                position: relative;
                display: inline-block;
                width: 30px;
                margin-top: 5px;
                margin-right: 20px;
                vertical-align: top;
                @media (max-width: 1400px) {
                  margin-right: 0;
                }
                i {
                  font-size: 20px;
                }
                &.done {
                  color: #8BC34A;
                }
                &.open {
                  color: #48A0DC;
                }
              }
              .notification {
                display: inline-block;
                @include css3-sufix(width, calc(100% - 55px));
                padding-bottom: 20px;
                border-bottom: 1px solid #D1D1D1;
                vertical-align: top;
                @media (max-width: 1400px) {
                  @include css3-sufix(width, calc(100% - 35px));
                }
                h4 {
                  display: inline-block;
                  margin-bottom: 0;
                  color: #333;
                  font-size: 14px;
                  font-weight: 600;
                }
                span {
                  display: inline-block;
                  color: #999;
                  font-size: 14px;
                }
                .day-data {
                  display: inline-block;
                  width: 100%;
                  color: #999;
                  font-size: 14px;
                }
                .btn-wrap {
                  display: inline-block;
                  width: 100%;
                  margin: 10px 0;
                  .poll {
                    padding: 6px 15px;
                    border-radius: 5px;
                    background-color: #48A0DC;
                    color: $white;
                    font-size: 14px;
                    text-transform: uppercase;
                  }
                }
              }
              &:last-child {
                .notification {
                  border-bottom: none;
                }
              }
            }
          }
        }
        .message-listing {
          .no-msg {
            display: inline-block;
            width: 100%;
            margin-bottom: 20px;
            padding: 5px 15px;
            border-radius: 5px;
            background-color: rgba(195, 74, 74, .2);
            color: rgb(195, 74, 74);
          }
          ul {
            li {
              display: inline-block;
              width: 100%;
              margin-bottom: 20px;
              .pro-img {
                display: inline-block;
                width: 50px;
                margin-right: 20px;
                vertical-align: middle;
                @media (max-width: 1400px) {
                  margin-right: 10px;
                }
                img {
                  height: 50px;
                  width: 50px;
                  background-color: $base-color;
                  border-radius: 100%;
                }
              }
              .pro-info {
                display: inline-block;
                @include css3-sufix(width, calc(100% - 84px));
                vertical-align: middle;
                h4 {
                  display: inline-block;
                  width: 100%;
                  margin-bottom: 0;
                  color: #333;
                  font-size: 13px;
                  font-weight: 600;
                }
                span {
                  display: inline-block;
                  width: 100%;
                  color: #999;
                  font-size: 12px;
                  a {
                    color: #999;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
#update-card-form,
#addCardModal {
  .modal-title {
    display: inline-block;
    width: 100%;
    color: $base-color;
    text-align: center;
  }
  .form-item {
    margin-bottom: 20px;
  }
  .form-action {
    .btn {
      margin-top: 0 !important;
      margin-bottom: 15px;
    }
  }
}


.latest-request-list {
  float: left;
  display: inline-block;
  width: 48%;
  margin-bottom: 50px;
  margin-right: 4%;
  padding: 40px 20px 30px;
  border-radius: 10px;
  background-color: $white;
  @include css3-prefix(box-shadow, 0 0 8px 0 rgba($base-color, .1));
  &:nth-child(2n) {
    margin-right: 0;
  }
  @media (max-width: 992px) {
    width: 100%;
    padding: 30px 20px;
    margin-right: 0;
    margin-bottom: 20px;
  }
  h2 {
    display: inline-block;
    width: 100%;
    margin-bottom: 30px;
    color: $primary-color;
    @media (max-width: 992px) {
      font-size: 20px;
    }
  }
  .table-responsive {
    display: inline-block;
    width: 100%;
    overflow: auto;
  }
  table {
    min-width: 100%;
    width: max-content;
    text-align: center;
    tr {
      th {
        color: $base-color;
      }
      th,
      td {
        padding: 10px;
        border-bottom: 1px solid #CECECE;
        span {
          display: block;
        }
        a.btn {
          min-width: 0;
          padding: 8px 20px;
          border-radius: 3px;
          font-size: 14px;
          &.cancel-btn {
            background-color: #ED5565;
          }
        }
      }
      td {
        img {
          border-radius: 10px;
        }
      }
    }
  }
}
table {
  tr {
    th,
    td {
      vertical-align: middle !important;
      .btn {
        margin: 3px;
        padding: 6px 9px;
      }
      img {
        height: auto !important;
      }
      &:last-child {
        min-width: 200px;
      }
    }
  }
}

.listing-page {
  .latest-request-list {
    width: 100%;
    margin-right: 0;
    margin-bottom: 0;
    box-shadow: none;
  }
}



/* Package Page Design */
.package-page {
  display: inline-block;
  min-height: calc(100vh - 110px);
  width: 100%;
  padding: 50px 50px 20px 50px;
  background-color: rgba($base-color, .1);
  @media (max-width: 992px) {
    padding: 30px 30px 20px 30px;
  }
  @media (max-width: 767px) {
    padding: 20px 15px;
  }
  .appointment-info {
    .grids {
      .header {
        background-color: $base-color;
      }
      ul {
        li {
          .button {
            color: #FFF;
            background-color: $secoundry-color;
            &:hover {
              color: #FFF;
              background-color: $secoundry-color;
            }
          }
        }
      }
    }
  }
}


/* Profile Edit page */
.profile-edit-page {
  display: inline-block;
  width: 100%;
  padding: 50px 50px 20px 50px;
  background-color: rgba($base-color, .1);
  min-height: calc(100vh - 110px);
  @media (max-width: 992px) {
    padding: 30px 30px 20px 30px;
  }
  @media (max-width: 767px) {
    padding: 20px 15px;
  }
  .card-header  {
    background-color: $base-color;
    color: $white;
    .btn {
      background-color: $secoundry-color;
      color: $white;
      border: none;
      &:hover {
        background-color: $secoundry-color !important;
        outline: none;
      }
    }
  }
  .form-control-label {
    display: inline-block;
    width: 100%;
  }
  .form-row {
    margin-right: -15px;
    margin-left: -15px;
  }
  .form-row>.col,
  .form-row>[class*=col-] {
    padding-right: 15px;
    padding-left: 15px;
  }
  .input-daterange {
    .input-group-addon,
    .form-control {
      display: inline-block;
      vertical-align: middle;
    }
    .input-group-addon {
      margin: 15px 6px 0;
    }
  }
  .opening_hours {
    .row {
      @include css3-sufix(align-items, center);
      margin-bottom: 20px;
    }
  }
}


.booking-details-page {
  display: inline-block;
  width: 100%;
  padding: 50px 50px 20px 50px;
  background-color: rgba($base-color, .1);
  @media (max-width: 992px) {
    padding: 30px 30px 20px 30px;
  }
  @media (max-width: 767px) {
    padding: 20px 15px;
  }
  .card-header {
    margin-bottom: 0 !important;
    background-color: $white;
  }
  .invoice-box {
    margin-bottom: 0;
    box-shadow: 0 2px 10px rgba(0, 0, 0, .15);
    table tr.top table td.title {
      padding-bottom: 0;
    }
    .information {
      a {
        color: $base-color;
      }
    }
  }
}

.notification-page {
  display: inline-block;
  min-height: calc(100vh - 110px);
  width: 100%;
  padding: 50px;
  /*background-color: rgba($base-color, .1);*/
  background-color: $white;
  @media (max-width: 992px) {
    padding: 30px;
  }
  @media (max-width: 767px) {
    padding: 20px 15px;
  }
  .account-header {
    margin-bottom: 20px;
    padding-bottom: 25px;
    @media (max-width: 767px) {
      padding-bottom: 0;
    }
    .dash-title {
      display: inline-block;
      margin-bottom: 0;
      @include css3-sufix(width, calc(100% - 198px));
      color: $base-color;
      vertical-align: middle;
      @media (max-width: 640px) {
        width: 100%;
      }
    }
    a {
      vertical-align: middle;
      @media (max-width: 767px) {
        float: right;
        font-size: 10px;
        padding: 6px 8px;
      }
    }
    .col-1 {
      a {
        float: none;
      }
    }
  }
  .account-body {
    .bill {
      display: inline-block;
      width: 100%;
      padding: 10px 0;
      border-bottom: 1px solid #CECECE;
      h4 {
        display: inline-block;
        margin-bottom: 0;
        padding-left: 10px;
        @include css3-sufix(width, calc(100% - 55px));
        font-size: 18px;
        vertical-align: middle;
        a {
          color: $primary-color;
        }
      }
      .input-rc {
        input[type='checkbox']+.input-rc-span {
          height: 20px;
          width: 18px;
        }
      }
      .btn {
        padding: 0;
        border: none;
        background-color: transparent;
        color: red;
        font-size: 16px;
        vertical-align: middle;
        &:focus,
        &:active,
        &:hover {
          background-color: transparent !important;
          color: red !important;
        }
      }
    }
    .pagination {
      margin-top: 30px;
    }
  }
}

.user-verification {
  padding-top: 50px;
  .card {
    background-color: $base-color !important;
    .card-body {
      color: $white;
      text-align: center;
      small {
        font-size: 20px;
        color: $white;
      }
      > div {
        font-size: 16px;
        color: $white;
      }
      .btn {
        display: inline-block;
        margin-top: 20px !important;
        padding: 8px 20px !important;
        background-color: $secoundry-color !important;
        white-space: normal;
        &:active,
        &:focus,
        &:hover {
          background-color: $secoundry-color !important;
          color: $white;
          opacity: 1;
          text-decoration: none;
        }
        @media (max-width: 640px) {
          font-size: 12px;
        }
      }
    }
  }
}


.card-header {
  @include css3-sufix(display, flex);
  @include css3-prefix(flex-direction, row);
  .titleText {
    flex: 1 1 auto;
    h3 {
      font-size: 26px;
      @media (max-width: 767px) {
        font-size: 20px;
      }
    }
  }
  .btn-wrap {
    width: 150px;
    text-align: right;
  }
}

.thankyou-content {
  display: inline-block;
  @include css3-sufix(min-height, calc(100vh - 180px)); 
  width: 100%;
  padding: 100px 0;
  text-align: center;
  img {
    max-width: 600px;
    width: 100%;
    margin-bottom: 30px;
  }
  p {
    font-size: 20px;
    font-weight: 600;
  }
}

.subscription-page {
  .card {
    .card-body {
      @include css3-sufix(display, flex);
      flex-wrap: wrap;
      width: 100%;
      justify-content: space-between;
    }
    .text {
      margin-bottom: 10px;
    }
    .account {
      @include css3-sufix(width, calc(25% - 15px));
      margin-bottom: 10px;
      padding: 10px;
      border: 1px solid #D8D8D8;
      border-radius: 8px;
      text-align: center;
      @media (max-width: 1366px) {
        @include css3-sufix(width, calc(50% - 20px));
      }
      @media (max-width: 640px) {
        width: 100%;
      }
      h4 {
        font-size: 22px;
        font-weight: 500;
      }

    }
  }
}
